import { LoginId } from 'backend-client-web';
import { TotpParameters } from 'client-core';

import { Size } from 'src/types';

import { IframeStateDescriptor } from './types';

export type TotpLogin = {
  id: LoginId;
  displayName: string;
  totpParameters?: TotpParameters;
};

export type TotpInputOverlayGeometry =
  | {
      type: 'rightOfInput';
    }
  | {
      type: 'overlayInput';
      buttonHeight: number;
    };

export type TotpInputIframeStateDescriptorType = {
  mode: 'totpInput';
  state: {
    expanded: boolean;
    overlayGeometry?: TotpInputOverlayGeometry;
    logins: TotpLogin[];
    intrinsicSize: Size;
    selectedLoginId?: LoginId;
  };
  action:
    | { type: 'setIntrinsicSize'; size: Size }
    | { type: 'setExpanded'; expanded: boolean }
    | { type: 'setSelectedLoginId'; selectedLoginId?: LoginId };
};

export const totpInputIframeStateDescriptor: IframeStateDescriptor<TotpInputIframeStateDescriptorType> = {
  mode: 'totpInput',
  initialState: {
    expanded: false,
    overlayGeometry: undefined,
    logins: [],
    intrinsicSize: { width: 0, height: 0 },
    selectedLoginId: undefined,
  },
  reducer: (state, action) => {
    switch (action.type) {
      case 'setExpanded':
        return { ...state, expanded: action.expanded };
      case 'setIntrinsicSize':
        return { ...state, intrinsicSize: action.size };
      case 'setSelectedLoginId':
        return { ...state, selectedLoginId: action.selectedLoginId };
    }
  },
};
