import { useEffect, useState } from 'react';
import browser from 'webextension-polyfill';

import { VaultId, trackError } from 'backend-client-web';

import {
  GetAutosnatchTargetsResponse,
  makeMessageListener,
  messageError,
  sendMessage,
} from 'src/message';

export default function useAutosnatchTargetOverride(
  targetOverride: VaultId | undefined,
  setAutosnatchTargetOverride: (targetVaultId: VaultId | undefined) => void,
) {
  const autosnatchTargetOptions = useAutosnatchTargetOptions();

  // Make sure the override is always valid (e.g. if a team is deleted or a profile disabled)
  useEffect(() => {
    if (!autosnatchTargetOptions) {
      return;
    }
    if (
      !autosnatchTargetOptions.targetGroups.find((g) =>
        g.vaults.find((v) => v.vaultId === targetOverride),
      )
    ) {
      setAutosnatchTargetOverride(undefined);
    }
  }, [autosnatchTargetOptions, targetOverride, setAutosnatchTargetOverride]);

  return {
    autosnatchTargetOverride: targetOverride,
    setAutosnatchTargetOverride,
    autosnatchTargetOptions,
  };
}

export function useAutosnatchTargetOptions() {
  const [targetOptions, setTargetOptions] = useState<GetAutosnatchTargetsResponse>();

  useEffect(() => {
    const updateTargetOptions = async () => {
      const resp = await sendMessage({ type: 'GetAutosnatchTargets' });
      setTargetOptions(resp === messageError ? undefined : resp);
    };

    updateTargetOptions().catch(trackError);

    const messageListener = makeMessageListener({ DataUpdate: updateTargetOptions });

    browser.runtime.onMessage.addListener(messageListener);
    return () => browser.runtime.onMessage.removeListener(messageListener);
  }, []);

  return targetOptions;
}
