import { Achievement } from 'backend-client-web';

import TotpQrOverlay from 'src/entrypoints/iframe/components/totpQrOverlay/TotpQrOverlay';
import { totpQrIframeStateDescriptor } from 'src/iframeState/descriptors/totpQr';
import useDispatchSetter from 'src/iframeState/hooks/useDispatchSetter';
import useIframeState from 'src/iframeState/hooks/useIframeState';
import { Size } from 'src/types';
import { copyToClipboardWithClear } from 'src/util/dom/copyToClipboard';
import useUnlock from 'src/util/react/useUnlock';

import useContentScriptConnection from './useContentScriptConnection';

export default function TotpQrOverlayContainer() {
  const port = useContentScriptConnection();

  const { iframeState, dispatch } = useIframeState(port, totpQrIframeStateDescriptor);

  const setIntrinsicSize = useDispatchSetter(dispatch, (size: Size) => ({
    type: 'setIntrinsicSize',
    size,
  }));

  const setHidden = useDispatchSetter(dispatch, (hidden: boolean) => ({
    type: 'setHidden',
    hidden,
  }));

  const { pushState, unlockDeviceType, addTotpParametersToLogin, cancelLogin } = useUnlock({
    achievement: Achievement.EXT_OVERLAY_UNLOCK,
  });

  const logins = iframeState.logins;
  const totpParameters = iframeState.totpParameters;

  if (!logins?.length || !totpParameters) {
    return null;
  }

  return (
    <TotpQrOverlay
      logins={logins.filter((l) => l.vaultMetadata?.id && l.permissions.editLogin)}
      totpParameters={totpParameters}
      addTotpToLogin={(loginId) => {
        const login = logins.find(({ id }) => id === loginId);
        if (!login?.vaultMetadata) {
          return;
        }
        return addTotpParametersToLogin(login.id, login.vaultMetadata.id, totpParameters);
      }}
      onClose={() => setHidden(true)}
      onCancel={cancelLogin}
      onResize={(width, height) => setIntrinsicSize({ width, height })}
      pushState={pushState === 'confirmed' ? null : pushState}
      unlockDeviceType={unlockDeviceType}
      copyToClipboardWithClear={copyToClipboardWithClear}
    />
  );
}
