import { addMilliseconds, differenceInMilliseconds } from 'date-fns';
import { useCallback, useEffect, useRef } from 'react';

import { VaultId } from 'backend-client-web';

import { autosnatchNotificationIframeStateDescriptor } from 'src/iframeState/descriptors/autosnatchNotification';
import useDispatchSetter from 'src/iframeState/hooks/useDispatchSetter';
import useIframeState from 'src/iframeState/hooks/useIframeState';
import { Size } from 'src/types';
import useAutosnatchSettings from 'src/util/react/useAutosnatchSettings';
import { useAutosnatchTargetOptions } from 'src/util/react/useAutosnatchTargetOverride';

import AutosnatchNotificationOverlay from '../components/autosnatchNotificationOverlay/AutosnatchNotificationOverlay';

import useContentScriptConnection from './useContentScriptConnection';

const AUTO_CLOSE_TIMEOUT = 5_000;

export default function AutosnatchNotificationOverlayContainer() {
  const port = useContentScriptConnection();

  const { iframeState, dispatch } = useIframeState(
    port,
    autosnatchNotificationIframeStateDescriptor,
  );

  const setIntrinsicSize = useDispatchSetter(dispatch, (size: Size) => ({
    type: 'setIntrinsicSize',
    size,
  }));

  const undo = useCallback(() => dispatch({ type: 'undo' }), [dispatch]);
  const close = useCallback(() => dispatch({ type: 'close' }), [dispatch]);
  const moveLogin = useCallback(
    (targetVaultId: VaultId) => dispatch({ type: 'moveLogin', targetVaultId }),
    [dispatch],
  );

  const setExpanded = useDispatchSetter(dispatch, (expanded: boolean) => ({
    type: 'setExpanded',
    expanded,
  }));

  const setMoveTargetInternal = useDispatchSetter(
    dispatch,
    (moveTargetInternal: VaultId | undefined) => ({
      type: 'setMoveTargetInternal',
      moveTargetInternal,
    }),
  );

  const setMoveSelected = useDispatchSetter(dispatch, (moveSelected: boolean) => ({
    type: 'setMoveSelected',
    moveSelected,
  }));

  const { cancelTimeout, resetTimeout, pauseTimeout, resumeTimeout } = useAutoclose(
    !!iframeState.uiState,
    close,
  );

  useEffect(() => {
    if (
      iframeState.uiState?.state === 'passwordChangeUndone' ||
      iframeState.uiState?.state === 'snatchUndone'
    ) {
      resetTimeout();
    }
  }, [iframeState.uiState, resetTimeout]);

  const autosnatchTargetOptions = useAutosnatchTargetOptions();

  const { autosnatchTarget, setAutosnatchTarget } = useAutosnatchSettings();

  if (!iframeState.uiState) {
    return null;
  }

  return (
    <AutosnatchNotificationOverlay
      setIntrinsicSize={setIntrinsicSize}
      {...iframeState.uiState}
      undo={undo}
      moveLogin={moveLogin}
      onClick={cancelTimeout}
      onMouseEnter={pauseTimeout}
      onMouseLeave={resumeTimeout}
      setExpanded={setExpanded}
      close={close}
      autosnatchTargetOptions={autosnatchTargetOptions}
      setMoveTargetInternal={setMoveTargetInternal}
      setMoveSelected={setMoveSelected}
      autosnatchTarget={autosnatchTarget}
      setAutosnatchTarget={setAutosnatchTarget}
    />
  );
}

function useAutoclose(isOpen: boolean, close: () => void) {
  const timeoutStateRef = useRef<
    | { state: 'active'; timeoutId: number; endsAt: Date }
    | { state: 'canceled' }
    | { state: 'paused'; remainingMs: number }
  >();

  const cancelTimeout = useCallback(() => {
    if (timeoutStateRef.current?.state === 'active') {
      window.clearTimeout(timeoutStateRef.current.timeoutId);
    }
    timeoutStateRef.current = { state: 'canceled' };
  }, []);

  const startTimeout = useCallback(
    (timeoutMs: number) => {
      cancelTimeout();
      timeoutStateRef.current = {
        state: 'active',
        timeoutId: window.setTimeout(() => {
          close();
          timeoutStateRef.current = undefined;
        }, timeoutMs),
        endsAt: addMilliseconds(new Date(), timeoutMs),
      };
    },
    [cancelTimeout, close],
  );

  const pauseTimeout = useCallback(() => {
    if (timeoutStateRef.current?.state !== 'active') {
      return;
    }

    window.clearTimeout(timeoutStateRef.current.timeoutId);
    const remainingMs = Math.max(
      differenceInMilliseconds(timeoutStateRef.current.endsAt, new Date()),
      0,
    );

    timeoutStateRef.current = { state: 'paused', remainingMs };
  }, []);

  const resumeTimeout = useCallback(() => {
    if (timeoutStateRef.current?.state !== 'paused') {
      return;
    }

    startTimeout(timeoutStateRef.current.remainingMs);
  }, [startTimeout]);

  const resetTimeout = useCallback(
    (unpause?: boolean) => {
      if (timeoutStateRef.current?.state === 'paused' && !unpause) {
        timeoutStateRef.current.remainingMs = AUTO_CLOSE_TIMEOUT;
      } else {
        cancelTimeout();
        startTimeout(AUTO_CLOSE_TIMEOUT);
      }
    },
    [cancelTimeout, startTimeout],
  );

  useEffect(() => {
    if (!isOpen) {
      cancelTimeout();
      timeoutStateRef.current = undefined;
      return;
    }

    if (timeoutStateRef.current) {
      return;
    }

    startTimeout(AUTO_CLOSE_TIMEOUT);
  }, [cancelTimeout, isOpen, startTimeout]);

  return {
    cancelTimeout,
    pauseTimeout,
    resumeTimeout,
    resetTimeout,
  };
}
