import { Size } from 'src/types';

import { IframeStateDescriptor } from './types';

export type RepeatPasswordIframeStateDescriptorType = {
  mode: 'repeatPassword';
  state: {
    intrinsicSize: Size;
    expanded: boolean;
    passwordInsertedTimestampMs?: number;
  };
  action:
    | { type: 'setIntrinsicSize'; size: Size }
    | { type: 'setExpanded'; expanded: boolean }
    | { type: 'hide' }
    | { type: 'insertPassword' };
};

export const repeatPasswordIframeStateDescriptor: IframeStateDescriptor<RepeatPasswordIframeStateDescriptorType> = {
  mode: 'repeatPassword',
  initialState: {
    intrinsicSize: { width: 0, height: 0 },
    expanded: false,
  },
  reducer: (state, action) => {
    switch (action.type) {
      case 'setIntrinsicSize':
        return { ...state, intrinsicSize: action.size };
      case 'setExpanded':
        return { ...state, expanded: action.expanded };
      case 'hide':
      case 'insertPassword':
        return state;
    }
  },
};
