import React, { useContext, PropsWithChildren, useRef, useCallback, useEffect } from 'react';

import { SelectElementState } from 'src/iframeState/descriptors/overrideCreate';
import { SelectorResults } from 'src/types';

const SelectorContext = React.createContext<{
  results: SelectorResults;
  selectElement: (tagNames?: string[]) => Promise<string>;
}>({ results: {}, selectElement: async () => '' });

export function SelectorContextProvider({
  children,
  selectorResults,
  selectElementState,
  setSelectElementState,
}: PropsWithChildren<{
  selectorResults: SelectorResults;
  selectElementState: SelectElementState | undefined;
  setSelectElementState: (s: SelectElementState | undefined) => void;
}>) {
  const selectElementPromiseRef = useRef<{
    promise: Promise<string>;
    resolve: (value: string) => void;
  }>();

  const selectElement = useCallback(
    (tagNames?: string[]) => {
      if (!selectElementPromiseRef.current) {
        let resolve: (value: string) => void;
        const promise = new Promise<string>((r) => {
          resolve = r;
        });
        selectElementPromiseRef.current = { promise, resolve: resolve! };
      }
      setSelectElementState({ state: 'inProgress', tagNames });
      return selectElementPromiseRef.current.promise;
    },
    [setSelectElementState],
  );

  useEffect(() => {
    if (selectElementState?.state === 'done' && selectElementPromiseRef.current) {
      selectElementPromiseRef.current.resolve(selectElementState.selector);
      selectElementPromiseRef.current = undefined;
    }
  });

  return (
    <SelectorContext.Provider value={{ results: selectorResults, selectElement }}>
      {children}
    </SelectorContext.Provider>
  );
}

export function useSelectorContext() {
  return useContext(SelectorContext);
}
