import { useCallback, useRef } from 'react';

import { IframeStateDescriptorType } from 'src/iframeState/descriptors';

export default function useDispatchSetter<A extends IframeStateDescriptorType['action'], P>(
  dispatch: (action: A) => void,
  makeAction: (param: P) => A,
) {
  const makeActionRef = useRef(makeAction);
  makeActionRef.current = makeAction;

  return useCallback(
    (param: P) => {
      dispatch(makeActionRef.current(param));
    },
    [dispatch],
  );
}
