import { t } from '@lingui/macro';
import classNames from 'classnames';
import { useCallback, useMemo } from 'react';

import { ProfileId, VaultId } from 'backend-client-web';
import { LabeledSuccessButton, NativeSelect } from 'client-web-sdk';

import { GetAutosnatchTargetsResponse } from 'src/message';

import styles from './AutosnatchTargetSelector.module.css';

interface AutosnatchTargetSelectorProps {
  // don't allow `undefined` for `autosnatchTargetOptions`
  autosnatchTargetOptions: GetAutosnatchTargetsResponse & {};
  autosnatchTarget: VaultId | undefined;
  setAutosnatchTarget: (value: VaultId) => void;
  autosnatchTargetOverride: VaultId | undefined;
  setAutosnatchTargetOverride: (value: VaultId) => void;
  className?: string;
  fixedSize?: boolean;
}

export default function AutosnatchTargetSelector({
  autosnatchTargetOptions,
  setAutosnatchTarget,
  autosnatchTarget,
  className,
  autosnatchTargetOverride,
  setAutosnatchTargetOverride,
  fixedSize,
}: AutosnatchTargetSelectorProps) {
  const selectedAccount = useMemo(
    () =>
      autosnatchTargetOptions.targetGroups.find((o) =>
        o.vaults.some((v) => v.vaultId === (autosnatchTargetOverride ?? autosnatchTarget)),
      ),
    [autosnatchTarget, autosnatchTargetOptions, autosnatchTargetOverride],
  );

  const selectFirstVault = useCallback(
    (profileId: ProfileId) => {
      const newAcc = autosnatchTargetOptions.targetGroups.find(
        (acc) => acc.profileId === profileId,
      );
      if (!newAcc?.vaults.length) {
        return;
      }

      setAutosnatchTargetOverride(newAcc.vaults[0].vaultId);
    },
    [autosnatchTargetOptions, setAutosnatchTargetOverride],
  );

  if (!shouldShowAutosnatchTargetSelector(autosnatchTargetOptions)) {
    return null;
  }

  return (
    <div className={classNames(styles.Container, className)}>
      {autosnatchTargetOptions.targetGroups.length > 1 ? (
        <NativeSelect
          className={styles.TargetSelector}
          wrapperClassName={styles.TargetSelectorWrapper}
          value={selectedAccount?.profileId}
          onChange={(e) => selectFirstVault(e.target.value)}
        >
          {autosnatchTargetOptions.targetGroups.map((opt) => (
            <option key={opt.profileId} value={opt.profileId}>
              {opt.accountLabel}
            </option>
          ))}
        </NativeSelect>
      ) : (
        <span className={styles.TargetPlaceholder}>{selectedAccount?.accountLabel}</span>
      )}
      {selectedAccount &&
        (selectedAccount.vaults.length > 1 ? (
          <NativeSelect
            onChange={(e) => setAutosnatchTargetOverride(e.target.value)}
            value={autosnatchTargetOverride ?? autosnatchTarget}
            wrapperClassName={styles.TargetSelectorWrapper}
            className={styles.TargetSelector}
          >
            {selectedAccount?.vaults.map((v) => (
              <option key={v.vaultId} value={v.vaultId}>
                {v.label}
              </option>
            ))}
          </NativeSelect>
        ) : (
          <span className={styles.TargetPlaceholder}>
            {
              selectedAccount.vaults.find(
                (v) => v.vaultId === (autosnatchTargetOverride ?? autosnatchTarget),
              )?.label
            }
          </span>
        ))}
      <LabeledSuccessButton
        className={classNames(styles.SaveTargetButton, {
          [styles.hidden]:
            !autosnatchTargetOverride || autosnatchTargetOverride === autosnatchTarget,
        })}
        visible={
          fixedSize || (!!autosnatchTargetOverride && autosnatchTargetOverride !== autosnatchTarget)
        }
        onClick={() => {
          if (autosnatchTargetOverride) {
            setAutosnatchTarget(autosnatchTargetOverride);
          }
        }}
        label={t`Save as default`}
        successLabel={t`Saved`}
      />
    </div>
  );
}

export function shouldShowAutosnatchTargetSelector(
  options: GetAutosnatchTargetsResponse & {},
): boolean {
  if (options.hasDisabledProfiles) {
    // Always show target if there are disabled profiles to reassure the user
    return true;
  }
  if (options.targetGroups.length === 0) {
    // Nothing to choose
    return false;
  }
  if (options.targetGroups.length === 1 && options.targetGroups[0].vaults.length < 2) {
    // Only single target
    return false;
  }
  return true;
}
