import { TotpParameters } from 'client-core';

import { ContentLogin, Size } from 'src/types';

import { IframeStateDescriptor } from './types';

export type TotpQrIframeStateDescriptorType = {
  mode: 'totpQr';
  state: {
    intrinsicSize: Size;
    hidden: boolean;
    logins?: readonly ContentLogin[];
    totpParameters?: TotpParameters;
  };
  action: { type: 'setIntrinsicSize'; size: Size } | { type: 'setHidden'; hidden: boolean };
};

export const totpQrIframeStateDescriptor: IframeStateDescriptor<TotpQrIframeStateDescriptorType> = {
  mode: 'totpQr',
  initialState: {
    hidden: false,
    intrinsicSize: { width: 0, height: 0 },
  },
  reducer: (state, action) => {
    switch (action.type) {
      case 'setIntrinsicSize':
        return { ...state, intrinsicSize: action.size };
      case 'setHidden':
        return { ...state, hidden: action.hidden };
    }
  },
};
