import { useCallback } from 'react';

import { Achievement, LoginId } from 'backend-client-web';

import { totpInputIframeStateDescriptor } from 'src/iframeState/descriptors/totpInput';
import useDispatchSetter from 'src/iframeState/hooks/useDispatchSetter';
import useIframeState from 'src/iframeState/hooks/useIframeState';
import { Size } from 'src/types';
import useCloseOnEscape from 'src/util/react/useCloseOnEscape';
import useUnlock from 'src/util/react/useUnlock';

import TotpInputOverlay from '../components/totpInputOverlay/TotpInputOverlay';

import useContentScriptConnection from './useContentScriptConnection';

export default function TotpInputOverlayContainer() {
  const port = useContentScriptConnection();

  const { iframeState, dispatch } = useIframeState(port, totpInputIframeStateDescriptor);

  const {
    requestUnlock,
    cancelLogin,
    pushState,
    unlockDeviceType,
    isUnlocked,
    unlockState,
  } = useUnlock({ achievement: Achievement.EXT_OVERLAY_UNLOCK });

  const setExpandedInternal = useDispatchSetter(dispatch, (expanded: boolean) => ({
    type: 'setExpanded',
    expanded,
  }));

  const setIntrinsicSize = useDispatchSetter(dispatch, (size: Size) => ({
    type: 'setIntrinsicSize',
    size,
  }));

  const setExpanded = useCallback(
    (expanded: boolean) => {
      if (expanded && !isUnlocked) {
        requestUnlock();
      } else if (!expanded && unlockState === 'unlocking') {
        cancelLogin();
      }
      setExpandedInternal(expanded);
    },
    [cancelLogin, isUnlocked, requestUnlock, setExpandedInternal, unlockState],
  );

  const selectLoginAndClose = useCallback(
    (loginId: LoginId) =>
      dispatch(
        { type: 'setSelectedLoginId', selectedLoginId: loginId },
        { type: 'setExpanded', expanded: false },
      ),
    [dispatch],
  );

  useCloseOnEscape(() => setExpanded(false));

  if (!iframeState.overlayGeometry || iframeState.logins.length === 0) {
    return null;
  }

  return (
    <TotpInputOverlay
      expanded={iframeState.expanded}
      setExpanded={setExpanded}
      logins={iframeState.logins}
      overlayGeometry={iframeState.overlayGeometry}
      setIntrinsicSize={setIntrinsicSize}
      selectLoginAndClose={selectLoginAndClose}
      unlockDeviceType={unlockDeviceType}
      pushState={pushState}
    />
  );
}
