import { Trans } from '@lingui/macro';
import classNames from 'classnames';
import { useEffect, useMemo, useState } from 'react';

import { LoginId, trackError } from 'backend-client-web';
import { getLoginDisplayLabel, TotpParameters } from 'client-core';
import {
  ActionButton,
  CopyButton,
  LoginWindow,
  NativeSelect,
  PushStateType,
  TotpCountdown,
  UIStyleContainer,
  UnlockDeviceType,
  useTotp,
} from 'client-web-sdk';

import useExtrinsicIframeSize from 'src/entrypoints/iframe/utils/getExtrinsicSizeFromLocation';
import { ContentLogin, CopyToClipboardWithClear } from 'src/types';
import { getUnprotectedValueForVaultWithContentId } from 'src/util/getUnprotectedValueForContentLogin';
import useCopyToClipboard from 'src/util/react/useCopyToClipboard';

import styles from './TotpQrOverlay.module.css';

interface Props {
  logins: readonly ContentLogin[];
  totpParameters: TotpParameters;
  pushState: PushStateType | null;
  unlockDeviceType: UnlockDeviceType;
  addTotpToLogin: (loginId: LoginId) => void;
  onResize?: (width: number, height: number) => void;
  onClose?: () => void;
  onCancel?: () => void;
  copyToClipboardWithClear: CopyToClipboardWithClear;
}

export default function TotpQrOverlay({
  logins,
  totpParameters,
  pushState,
  unlockDeviceType,
  addTotpToLogin,
  onResize,
  onClose,
  onCancel,
  copyToClipboardWithClear,
}: Props) {
  const [selectedLoginId, setSelectedLoginId] = useState<LoginId>(logins[0].id);
  useEffect(() => {
    if (!logins.some(({ id }) => id === selectedLoginId)) {
      setSelectedLoginId(logins[0].id);
    }
  }, [logins, selectedLoginId]);
  const selectedLogin = useMemo(
    () => logins.find(({ id }) => id === selectedLoginId) ?? logins[0],
    [logins, selectedLoginId],
  );

  const [saving, setSaving] = useState(false);
  const [totpUpdated, setTotpUpdated] = useState(false);
  useEffect(() => {
    async function updateStatus() {
      const selectedLoginTotp =
        selectedLogin.vaultMetadata?.id && selectedLogin.totp
          ? await getUnprotectedValueForVaultWithContentId(
              selectedLogin.vaultMetadata?.id,
              selectedLogin.totp,
            )
          : undefined;
      if (saving && selectedLoginTotp?.unencrypted === totpParameters.secret) {
        setTotpUpdated(true);
      } else if (totpUpdated && selectedLoginTotp?.unencrypted !== totpParameters.secret) {
        setTotpUpdated(false);
        setSaving(false);
      }
    }
    updateStatus().catch(trackError);
  }, [saving, selectedLogin.totp, selectedLogin.vaultMetadata?.id, totpParameters, totpUpdated]);

  const { totp, totpPercent, totpDigits } = useTotp(
    totpParameters.secret,
    totpParameters.algorithm,
    totpParameters.digits,
    totpParameters.period,
  );

  const [copied, copy] = useCopyToClipboard(copyToClipboardWithClear);

  const { width: extrinsicWidth } = useExtrinsicIframeSize();

  return (
    <LoginWindow
      state={pushState}
      onResize={onResize}
      onClose={onClose}
      onCancel={
        onCancel
          ? () => {
              setSaving(false);
              onCancel();
            }
          : undefined
      }
      onRetry={() => {
        addTotpToLogin(selectedLoginId);
        setSaving(true);
      }}
      cornerBackground
      cornerBackgroundTransparent
      unlockDeviceType={unlockDeviceType}
    >
      <UIStyleContainer
        uiStyle="highlight"
        className={styles.Content}
        style={{
          // @ts-ignore
          '--extrinsic-width': `${extrinsicWidth}px`,
        }}
      >
        <div className={styles.TargetLogin}>
          <div className={styles.TargetName}>
            <Trans>TOTP secret for</Trans>
          </div>
          <div className={styles.TargetName}>
            {logins.length > 1 ? (
              <NativeSelect
                className={styles.Select}
                onChange={(e) => setSelectedLoginId(e.currentTarget.value)}
                value={selectedLoginId}
                disabled={totpUpdated}
              >
                {logins.map((l) => (
                  <option value={l.id} key={l.id}>
                    {getLoginDisplayLabel(l) ?? <Trans>Unnamed login</Trans>}
                  </option>
                ))}
              </NativeSelect>
            ) : (
              getLoginDisplayLabel(selectedLogin) ?? <Trans>Unnamed login</Trans>
            )}
          </div>
        </div>
        <div>
          <ActionButton
            buttonStyle="secondary"
            size="small"
            type="button"
            onClick={() => {
              addTotpToLogin(selectedLoginId);
              setSaving(true);
            }}
            disabled={saving}
            className={classNames(styles.SaveButton, { [styles.saved]: totpUpdated })}
          >
            {totpUpdated ? (
              <Trans>Saved</Trans>
            ) : saving ? (
              <Trans>Saving</Trans>
            ) : (
              <Trans>Add to login</Trans>
            )}
          </ActionButton>
        </div>
        <div className={styles.TotpCodeContainer}>
          <div className={styles.TotpValue}>
            {totpUpdated && totp ? (
              <>
                <span>{totp.substring(0, totpDigits / 2)}</span>
                <span>{totp.substring(totpDigits / 2)}</span>
              </>
            ) : (
              <>
                <span>{'?'.repeat(totpDigits / 2)}</span>
                <span>{'?'.repeat(totpDigits / 2 + (totpDigits % 2))}</span>
              </>
            )}
            <TotpCountdown percent={totpPercent ?? 0} hidden={!(totpPercent && totpUpdated)} />
          </div>
          <div className={styles.TotpCopy}>
            <CopyButton
              copied={copied}
              onClick={() => totp && copy(totp)}
              disabled={!totpUpdated}
            />
          </div>
        </div>
      </UIStyleContainer>
    </LoginWindow>
  );
}
