import React, { useCallback } from 'react';

import { UUID, WebpageOverride } from 'persistable-types';

import OverrideCreateOverlay from 'src/entrypoints/iframe/components/overrideCreateOverlay/OverrideCreateOverlay';
import {
  overrideCreateIframeStateDescriptor,
  SelectElementState,
} from 'src/iframeState/descriptors/overrideCreate';
import useDispatchSetter from 'src/iframeState/hooks/useDispatchSetter';
import useIframeState from 'src/iframeState/hooks/useIframeState';
import { sendMessage } from 'src/message';
import { Size } from 'src/types';

import useContentScriptConnection from './useContentScriptConnection';

export default function OverrideCreateOverlayContainer() {
  const port = useContentScriptConnection();

  const { iframeState, dispatch } = useIframeState(port, overrideCreateIframeStateDescriptor);

  const setOverride = useDispatchSetter(dispatch, (override: WebpageOverride | undefined) => ({
    type: 'setOverride',
    override,
  }));

  const setIntrinsicSize = useDispatchSetter(dispatch, (size: Size) => ({
    type: 'setIntrinsicSize',
    size,
  }));

  const setSelectElementState = useDispatchSetter(
    dispatch,
    (selectElementState: SelectElementState | undefined) => ({
      type: 'setSelectElementState',
      selectElementState,
    }),
  );

  const replaceOverride = useCallback(async (override: WebpageOverride) => {
    await sendMessage({ type: 'ReplaceOverrides', overrides: [override] });
  }, []);

  const deleteOverride = useCallback(async (overrideId: UUID) => {
    await sendMessage({ type: 'DeleteOverrides', overrideIds: [overrideId] });
  }, []);

  const setAlign = useDispatchSetter(dispatch, (align: 'left' | 'right') => ({
    type: 'setAlign',
    align,
  }));

  return (
    <OverrideCreateOverlay
      appliedInfo={iframeState.appliedInfo}
      hostname={iframeState.hostname}
      override={iframeState.override}
      selectElementState={iframeState.selectElementState}
      selectorResults={iframeState.selectorResults}
      overrideIsReplace={iframeState.overrideIsReplace}
      setIntrinsicSize={setIntrinsicSize}
      setOverride={setOverride}
      setSelectElementState={setSelectElementState}
      replaceOverride={replaceOverride}
      deleteOverride={deleteOverride}
      setAlign={setAlign}
      align={iframeState.align}
    />
  );
}
