import { t } from '@lingui/macro';
import React from 'react';

import { Size } from 'src/types';

import PasswordFieldOverlay from './PasswordFieldOverlay';
import usePillText from './usePillText';

interface Props {
  generatorExpanded: boolean;
  passwordFieldHeight: number;
  passwordFieldWidth: number;
  passwordInsertedTimestampMs?: number;
  onButtonClick: () => void;
  onClose?: () => void;
  onResize?: (size: Size) => void;
}

export default function RepeatedPasswordFieldOverlay(props: Props) {
  const pillProps = usePillText({
    passwordInsertedTimestampMs: props.passwordInsertedTimestampMs,
    generatorExpanded: props.generatorExpanded,
    passwordFieldWidth: props.passwordFieldWidth,
    strengthIndication: null,
    strengthIndicationFallback: t`Repeated password`,
  });

  return <PasswordFieldOverlay {...props} {...pillProps} />;
}
