import { VaultId } from 'backend-client-web';
import { LoginId, TotpParameters } from 'client-core';

import { ContentLogin, Size } from 'src/types';

import { IframeStateDescriptor } from './types';

export type LoginIframeStateDescriptorType = {
  mode: 'login';
  state: {
    intrinsicSize: Size;
    logins?: readonly ContentLogin[];
    preferredLoginIds?: LoginId[];
  };
  action:
    | { type: 'setIntrinsicSize'; size: Size }
    | { type: 'hide' }
    | {
        type: 'updateLoginAndFillForm';
        targetLoginId: LoginId;
        targetVaultId?: VaultId;
        username: string;
        password: string;
        totp?: TotpParameters;
      };
};

export const loginIframeStateDescriptor: IframeStateDescriptor<LoginIframeStateDescriptorType> = {
  mode: 'login',
  initialState: {
    intrinsicSize: { width: 0, height: 0 },
  },
  reducer: (state, action) => {
    switch (action.type) {
      case 'setIntrinsicSize':
        return { ...state, intrinsicSize: action.size };
      case 'hide':
      case 'updateLoginAndFillForm':
        return state;
    }
  },
};
