import { Trans } from '@lingui/macro';
import React from 'react';

import { LoginButton } from 'client-web-sdk';

import useErrorHandlingCallback from 'src/util/react/useErrorHandlingCallback';

import styles from './ConfirmLoginUse.module.css';

interface Props {
  confirm: () => Promise<void>;
  loginLink?: string;
  label: string;
}

export default function ConfirmLoginUse({ loginLink, label, confirm }: Props) {
  const handleAcceptClick = useErrorHandlingCallback(() => confirm(), [confirm]);
  return (
    <div className={styles.ConfirmLoginUse}>
      <h1 className={styles.Headline}>
        <Trans>Use new login from your inbox?</Trans>
      </h1>
      <p>
        <LoginButton onClick={handleAcceptClick} className={styles.LoginButton} icon="inbox">
          <div>{label}</div>
        </LoginButton>
      </p>
      {loginLink && (
        <p>
          <a href={loginLink} target="_blank" rel="noreferrer" className={styles.ShowLoginLink}>
            <Trans>View login</Trans>
          </a>
        </p>
      )}
    </div>
  );
}
