import { useEffect, useState } from 'react';

import { trackError } from 'backend-client-web';

import {
  openConnection,
  ForwardIframeClientPort,
  ForwardIframeConnectionDescriptor,
} from 'src/connections';

export default function useContentScriptConnection() {
  const [port, setPort] = useState<ForwardIframeClientPort>();
  useEffect(() => {
    const connectionId = new URLSearchParams(window.location.search).get('connectionId');
    if (!connectionId) {
      trackError(new Error('Iframe opened without connectionId'));
      return;
    }
    const newPort = openConnection<ForwardIframeConnectionDescriptor>(
      'ForwardIframe',
      connectionId,
    );
    setPort(newPort);
  }, []);
  return port;
}
