import { OverridePredicate } from 'persistable-types';

import SelectorInput from './SelectorInput';

export default function PredicateEdit({
  predicate,
  setPredicate,
}: {
  predicate: OverridePredicate | undefined;
  setPredicate: (p: OverridePredicate | undefined) => void;
}) {
  if (!predicate) {
    return (
      <div>
        <button type="button" onClick={() => setPredicate({ type: 'elementExists', selector: '' })}>
          Add predicate
        </button>
      </div>
    );
  }
  return (
    <fieldset>
      <legend>Predicate: {predicate.type}</legend>
      <div>
        <button type="button" onClick={() => setPredicate(undefined)}>
          Delete predicate
        </button>{' '}
        <label>
          <input
            type="checkbox"
            checked={predicate.negated}
            onChange={(e) => setPredicate({ ...predicate, negated: e.currentTarget.checked })}
          />{' '}
          Negated
        </label>
      </div>
      {predicate.type === 'elementExists' ? (
        <div>
          <div>
            <label>
              Selector:{' '}
              <SelectorInput
                selector={predicate.selector}
                setSelector={(selector) => setPredicate({ ...predicate, selector })}
              />
            </label>
          </div>
          <div>
            <label>
              <input
                type="checkbox"
                checked={predicate.requireVisible}
                onChange={(e) =>
                  setPredicate({ ...predicate, requireVisible: e.currentTarget.checked })
                }
              />{' '}
              Require element visible
            </label>
          </div>
        </div>
      ) : (
        <div>Not supported</div>
      )}
    </fieldset>
  );
}
