import { Trans } from '@lingui/macro';
import classNames from 'classnames';
import React, { useMemo, useEffect, useRef } from 'react';

import { VaultId } from 'backend-client-web';
import { useElementSize, Switch } from 'client-web-sdk';

import { ReactComponent as ArrowDownIcon } from 'src/images/arrow_down.svg';
import { ReactComponent as ArrowUpIcon } from 'src/images/arrow_up.svg';
import { ReactComponent as HeyloginIconColor } from 'src/images/heylogin_icon.svg';
import { ReactComponent as HeyloginIconGray } from 'src/images/heylogin_icon_disabled.svg';
import { GetAutosnatchTargetsResponse } from 'src/message';

import AutosnatchTargetSelector, {
  shouldShowAutosnatchTargetSelector,
} from '../AutosnatchTargetSelector';

import styles from './AutosnatchOverlay.module.css';
import { ReactComponent as EyeOffIcon } from './eye-off.svg';
import { ReactComponent as LockIcon } from './lock.svg';
import { ReactComponent as LoginOverlayIcon } from './login-overlay.svg';

interface MenuProps {
  openPasswordGenerator: () => void;
  setAutosnatchActive: (active: boolean) => void;
  autosnatchActive: boolean;
  autosnatchTargetOptions: GetAutosnatchTargetsResponse;
  autosnatchTarget: VaultId | undefined;
  setAutosnatchTarget: (newTarget: VaultId) => void;
  autosnatchTargetOverride: VaultId | undefined;
  setAutosnatchTargetOverride: (target: VaultId) => void;
  close: () => void;
  pageHasLogins: boolean;
  showLoginOverlay: () => void;
}

type Props = {
  expanded: boolean;
  setExpanded: (expanded: boolean) => void;
  onResize: (width: number, height: number) => void;
  passwordFieldHeight: number;
} & MenuProps;

export default function AutosnatchOverlay({
  onResize,
  setAutosnatchActive: setActive,
  setExpanded,
  openPasswordGenerator,
  expanded,
  autosnatchActive: active,
  passwordFieldHeight,
  ...menuProps
}: Props) {
  const ref = useRef<HTMLDivElement | null>(null);

  const [rootRef, size] = useElementSize();
  useEffect(() => {
    onResize(size.width, size.height);
  }, [size.height, size.width, onResize]);

  const fontSize = useMemo(() => passwordFieldHeight / 2, [passwordFieldHeight]);

  return (
    <div
      className={styles.Container}
      ref={rootRef}
      onClick={(e) => {
        // Close when clicking in the empty space between menu and button. In these cases, container
        // itself is the click event's target.
        if (e.target === ref.current) {
          setExpanded(false);
        }
      }}
    >
      <button
        onClick={() => setExpanded(!expanded)}
        className={styles.Button}
        style={{ fontSize: `${fontSize}px` }}
      >
        {active ? (
          <HeyloginIconColor className={styles.HeyloginIcon} />
        ) : (
          <HeyloginIconGray className={styles.HeyloginIcon} />
        )}
        {expanded ? (
          <ArrowUpIcon className={styles.Arrow} />
        ) : (
          <ArrowDownIcon className={styles.Arrow} />
        )}
      </button>
      {expanded && (
        <AutosnatchOverlayMenu
          autosnatchActive={active}
          openPasswordGenerator={openPasswordGenerator}
          setAutosnatchActive={setActive}
          {...menuProps}
        />
      )}
    </div>
  );
}

function AutosnatchOverlayMenu({
  autosnatchActive: active,
  setAutosnatchActive: setActive,
  openPasswordGenerator,
  autosnatchTarget,
  autosnatchTargetOptions,
  setAutosnatchTarget,
  autosnatchTargetOverride,
  setAutosnatchTargetOverride,
  close,
  pageHasLogins,
  showLoginOverlay,
}: MenuProps) {
  return (
    <div className={styles.Menu}>
      <label className={styles.ToggleActive}>
        <Switch
          className={styles.Switch}
          onChange={(e) => setActive(e.target.checked)}
          checked={active}
        />
        <span className={styles.ToggleActiveLabel}>
          <Trans>Enable autosave on this device</Trans>
        </span>
      </label>
      {active &&
        autosnatchTargetOptions &&
        shouldShowAutosnatchTargetSelector(autosnatchTargetOptions) && (
          <div className={styles.TargetSelectorGroup}>
            <div className={styles.ToLabel}>
              <Trans>To</Trans>
            </div>
            <AutosnatchTargetSelector
              className={styles.TargetSelector}
              autosnatchTarget={autosnatchTarget}
              autosnatchTargetOverride={autosnatchTargetOverride}
              setAutosnatchTargetOverride={setAutosnatchTargetOverride}
              autosnatchTargetOptions={autosnatchTargetOptions}
              setAutosnatchTarget={setAutosnatchTarget}
            />
          </div>
        )}
      <button
        type="button"
        onClick={openPasswordGenerator}
        className={classNames(styles.MenuButton, styles.primary)}
      >
        <LockIcon />
        <Trans>Generate password</Trans>
      </button>
      {pageHasLogins && (
        <button
          type="button"
          onClick={showLoginOverlay}
          className={classNames(styles.MenuButton, styles.primary)}
        >
          <LoginOverlayIcon />
          <Trans>Open login window</Trans>
        </button>
      )}
      <button type="button" onClick={close} className={classNames(styles.MenuButton, styles.hide)}>
        <EyeOffIcon />
        <Trans>Hide heylogin once on this page</Trans>
      </button>
    </div>
  );
}
