import { useState } from 'react';

import { OverrideRule } from 'persistable-types';

import { ruleTypes, emptyRules } from './defaultValues';

export default function RuleCreate({ addRule }: { addRule: (rule: OverrideRule) => void }) {
  const [ruleType, setRuleType] = useState<OverrideRule['type'] | 'initial'>('initial');
  return (
    <div>
      <label>
        Rule Type:{' '}
        <select
          value={ruleType}
          onChange={(e) => setRuleType(e.currentTarget.value as OverrideRule['type'])}
        >
          <option value="initial">Select type</option>
          {ruleTypes.map((t) => (
            <option value={t} key={t}>
              {t}
            </option>
          ))}
        </select>
      </label>{' '}
      <button
        type="button"
        onClick={() => {
          if (ruleType !== 'initial') {
            addRule(emptyRules[ruleType]);
            setRuleType('initial');
          }
        }}
        disabled={ruleType === 'initial'}
      >
        Add new rule here
      </button>
    </div>
  );
}
