import { Trans } from '@lingui/macro';
import React from 'react';

import { LoginButton, TotpTimeOffsetWarning } from 'client-web-sdk';

import styles from './OverlayTotpWarning.module.css';

export default function OverlayTotpWarning({
  performLogin,
  close,
  warningCleared,
  recheck,
}: {
  performLogin: () => void;
  close: () => void;
  warningCleared: boolean;
  recheck: () => Promise<void>;
}) {
  return (
    <>
      <TotpTimeOffsetWarning
        className={styles.OverlayTotpWarning}
        onClose={close}
        hidden={warningCleared}
        recheck={recheck}
        onRecheckSuccess={performLogin}
      >
        <LoginButton size="small" onClick={performLogin} className={styles.LoginButton}>
          <Trans>Login anyways</Trans>
        </LoginButton>
      </TotpTimeOffsetWarning>
    </>
  );
}
