import classNames from 'classnames';
import React, { useEffect, useMemo, PropsWithChildren } from 'react';

import { Spinner, useElementSize } from 'client-web-sdk';

import { ReactComponent as Close } from 'src/images/close.svg';
import { ReactComponent as EyeOffIcon } from 'src/images/eye-off.svg';
import { ReactComponent as EyeIcon } from 'src/images/eye.svg';
import { ReactComponent as HeyloginIcon } from 'src/images/heylogin_icon.svg';
import { ReactComponent as PillImage } from 'src/images/password_generator_pill_image.svg';
import { Size } from 'src/types';

import styles from './PasswordFieldOverlay.module.css';

type Props = PropsWithChildren<{
  eyeIcon?: 'on' | 'off';
  passwordFieldHeight: number;
  passwordFieldWidth: number;
  pillText?: string;
  spinner?: boolean;
  successBackground?: boolean;
  onButtonClick: () => void;
  onClose?: () => void;
  onResize?: (size: Size) => void;
}>;

export default function PasswordFieldOverlay({
  children,
  eyeIcon,
  passwordFieldHeight,
  passwordFieldWidth,
  pillText,
  spinner,
  successBackground,
  onButtonClick,
  onClose,
  onResize,
}: Props) {
  const [sizeRoot, size] = useElementSize<HTMLDivElement>();
  useEffect(() => {
    if (onResize) {
      onResize(size);
    }
  }, [onResize, size]);

  const infoFontSizeRem = useMemo(() => {
    const baseSize = Math.min(passwordFieldWidth / 300, passwordFieldHeight / 50);
    return Math.min(Math.max(baseSize, 0.7), 1.25);
  }, [passwordFieldHeight, passwordFieldWidth]);
  return (
    <div
      className={styles.PasswordFieldOverlay}
      ref={sizeRoot}
      style={{
        minWidth: `${passwordFieldWidth}px`,
      }}
    >
      <div
        className={classNames(styles.Info, {
          [styles.successBackground]: successBackground,
        })}
        style={{
          minHeight: `${passwordFieldHeight}px`,
          fontSize: `${infoFontSizeRem}rem`,
        }}
      >
        <button type="button" className={styles.SecurePasswordPill} onClick={onButtonClick}>
          {spinner ? <Spinner size="1.5em" /> : <HeyloginIcon className={styles.HeyloginIcon} />}
          <span className={styles.PillText}>{pillText ?? <PillImage />}</span>
          {eyeIcon === 'off' ? (
            <EyeOffIcon className={styles.EyeIcon} />
          ) : eyeIcon === 'on' ? (
            <EyeIcon className={styles.EyeIcon} />
          ) : null}
        </button>

        {onClose && (
          <button type="button" onClick={onClose} className={styles.Close}>
            <Close aria-label="Close" />
          </button>
        )}
      </div>
      {children}
    </div>
  );
}
