import { Trans } from '@lingui/macro';
import classNames from 'classnames';
import React from 'react';

import { SubscriptionError } from 'backend-client-web';
import { ActionButton, UIStyleContextProvider } from 'client-web-sdk';

import styles from './SubscriptionNag.module.css';

export default function SubscriptionNag({
  subscriptionSettingsLink,
  organizationName,
  landscape,
  subscriptionError,
}: {
  subscriptionSettingsLink?: string;
  organizationName: string;
  landscape?: boolean;
  subscriptionError: SubscriptionError | undefined;
}) {
  return (
    <UIStyleContextProvider uiStyle="highlight">
      <div className={classNames(styles.SubscriptionNag, { [styles.landscape]: landscape })}>
        <h1 className={styles.Headline}>
          {subscriptionError === SubscriptionError.TRIAL_EXCEEDED ? (
            <Trans>Trial Expired</Trans>
          ) : (
            <Trans>Plan paused</Trans>
          )}
        </h1>
        <p className={styles.Info}>
          {subscriptionError === SubscriptionError.TRIAL_EXCEEDED ? (
            <Trans>
              The heylogin trial for <strong>{organizationName}</strong> has expired.
            </Trans>
          ) : (
            <Trans>
              The heylogin plan of your organization <strong>{organizationName}</strong> is
              paused.
            </Trans>
          )}
        </p>
        {subscriptionSettingsLink && (
          <div className={styles.ButtonGroup}>
            <ActionButton
              element="a"
              buttonStyle="primary"
              href={subscriptionSettingsLink}
              target="_blank"
              className={styles.UpgradeButton}
              rel="noreferrer"
            >
              {subscriptionError === SubscriptionError.TRIAL_EXCEEDED ? (
                <Trans>Upgrade now</Trans>
              ) : (
                <Trans>Manage plan</Trans>
              )}
            </ActionButton>
          </div>
        )}
      </div>
    </UIStyleContextProvider>
  );
}
