import { t } from '@lingui/macro';
import React, { useState, useCallback, useMemo, useRef, useEffect } from 'react';

import { Achievement, trackError } from 'backend-client-web';
import { LoginId, getLoginDisplayLabel } from 'client-core';
import { LoginWindow } from 'client-web-sdk';

import { ContentToIframeMessage } from 'src/connections';
import UpdateLoginForm from 'src/entrypoints/iframe/components/UpdateLoginForm';
import useExtrinsicIframeSize from 'src/entrypoints/iframe/utils/getExtrinsicSizeFromLocation';
import { loginIframeStateDescriptor } from 'src/iframeState/descriptors/login';
import useDispatchSetter from 'src/iframeState/hooks/useDispatchSetter';
import useIframeState from 'src/iframeState/hooks/useIframeState';
import { sendMessage } from 'src/message';
import { getFrontendServerUrl } from 'src/serverUrl';
import { ContentLogin, LoginOrganizationMetadata, Size } from 'src/types';
import addAchievement from 'src/util/addAchievement';
import getUnprotectedValueForContentLogin from 'src/util/getUnprotectedValueForContentLogin';
import ClientOutdatedMessage from 'src/util/react/ClientOutdatedMessage';
import useClientOutdated from 'src/util/react/useClientOutdated';
import useCloseOnEscape from 'src/util/react/useCloseOnEscape';

import { getLoginOverlayLandscape } from '../../../util/getLoginOverlayLandscape';
import useUnlock from '../../../util/react/useUnlock';
import shouldAskForLoginUsername from '../../../util/shouldAskForLoginUsername';
import ConfirmLoginUse from '../components/ConfirmLoginUse';
import OverlayLoginList from '../components/OverlayLoginList';
import OverlayTotpWarning from '../components/OverlayTotpWarning';
import SubscriptionNag from '../components/SubscriptionNag';
import useParentPageUrl from '../utils/useParentPageUrl';

import useContentScriptConnection from './useContentScriptConnection';

export default function LoginOverlay() {
  const port = useContentScriptConnection();

  const { iframeState, dispatch } = useIframeState(port, loginIframeStateDescriptor);

  const setIntrinsicSize = useDispatchSetter(dispatch, (size: Size) => ({
    type: 'setIntrinsicSize',
    size,
  }));

  const { host, url } = useParentPageUrl();

  const { performLogin, cancelLogin, requestUnlock, pushState, unlockDeviceType } = useUnlock({
    achievement: Achievement.EXT_OVERLAY_UNLOCK,
  });

  const [selectedLoginId, setSelectedLoginId] = useState<LoginId>();
  const [confirmUseLogin, setConfirmUseLogin] = useState<ContentLogin>();
  const [addUsernameLoginId, setAddUsernameLoginId] = useState<LoginId>();
  const [subscriptionNagLogin, setSubscriptionNagLogin] = useState<
    ContentLogin & { organization: LoginOrganizationMetadata }
  >();
  const [totpTimeOffsetWarningLoginId, setTotpTimeOffsetWarningLoginId] = useState<LoginId>();

  const subscriptionSettingsLink = useMemo(() => {
    const orgInfo = subscriptionNagLogin?.organization;
    if (!orgInfo) {
      return undefined;
    }
    const error = orgInfo.subscriptionError;
    if (!error) {
      return undefined;
    }

    if (!orgInfo.isAdmin) {
      return undefined;
    }

    return `${getFrontendServerUrl()}/organizations/${orgInfo.id}/manage/plan`;
  }, [subscriptionNagLogin]);

  const handleLoginClick = useCallback(
    (login: ContentLogin) => {
      setSelectedLoginId(login.id);
      addAchievement(Achievement.EXT_OVERLAY_LOGIN);
      if (login.pendingInboxConfirmation) {
        setConfirmUseLogin(login);
      } else if (shouldAskForLoginUsername(login)) {
        requestUnlock();
        // short timeout so we don't show the UpdateLoginForm for a moment before switching to the
        // unlock progress view.
        window.setTimeout(() => setAddUsernameLoginId(login.id), 10);
      } else if (login.organization?.subscriptionError) {
        setSubscriptionNagLogin(
          login as ContentLogin & { organization: LoginOrganizationMetadata },
        );
        addAchievement(Achievement.EXT_OVERLAY_SAW_NAG_SCREEN);
      } else if (login.showTotpTimeOffsetWarning) {
        setTotpTimeOffsetWarningLoginId(login.id);
      } else {
        performLogin(login.id);
      }
    },
    [performLogin, requestUnlock],
  );

  const handleResize = useCallback(
    (width: number, height: number) => {
      setIntrinsicSize({ width, height });
    },
    [setIntrinsicSize],
  );

  const [newLoginSuccess, setNewLoginSuccess] = useState(false);

  const handleClose = useMemo(() => {
    if (pushState === 'confirmed') {
      return undefined;
    }
    return () => dispatch({ type: 'hide' });
  }, [dispatch, pushState]);
  useCloseOnEscape(handleClose);

  const { landscape, extrinsicHeight } = useOverlayLayout();

  const [addUsernameLogin, setAddUsernameLogin] = useState<
    ContentLogin & {
      unprotectedPassword: string;
    }
  >();
  useEffect(() => {
    if (addUsernameLogin?.id === addUsernameLoginId) {
      return;
    }
    async function updateAddUsernameLogin() {
      const login = iframeState.logins?.find(({ id }) => id === addUsernameLoginId);
      if (!login) {
        setAddUsernameLogin(undefined);
        return;
      }
      const unprotectedPassword = await getUnprotectedValueForContentLogin(login, login?.password);
      if (!unprotectedPassword) {
        setAddUsernameLogin(undefined);
        return;
      }
      setAddUsernameLogin({ ...login, unprotectedPassword });
    }
    updateAddUsernameLogin().catch(trackError);
  }, [addUsernameLogin?.id, addUsernameLoginId, iframeState.logins]);

  const clientOutdated = useClientOutdated();

  const performDefaultActionRef = useRef(() => {});
  useEffect(() => {
    if (!port) {
      return undefined;
    }
    function handleMessage(msg: ContentToIframeMessage) {
      if (msg.type === 'PerformEnterDefaultAction') {
        performDefaultActionRef.current();
      }
    }
    port.onMessage.addListener(handleMessage);
    return () => port.onMessage.removeListener(handleMessage);
  }, [port]);

  return (
    <LoginWindow
      onClose={addUsernameLoginId ? undefined : handleClose}
      onResize={handleResize}
      onRetry={() => {
        if (addUsernameLoginId) {
          requestUnlock();
        } else if (selectedLoginId) {
          performLogin(selectedLoginId);
        }
      }}
      onCancel={cancelLogin}
      state={newLoginSuccess ? 'confirmed' : pushState}
      landscape={landscape}
      cornerBackground={!!addUsernameLoginId || !!subscriptionNagLogin || !!confirmUseLogin}
      onBack={
        pushState
          ? undefined
          : addUsernameLoginId
          ? () => setAddUsernameLoginId(undefined)
          : confirmUseLogin
          ? () => setConfirmUseLogin(undefined)
          : subscriptionNagLogin
          ? () => setSubscriptionNagLogin(undefined)
          : undefined
      }
      unlockDeviceType={unlockDeviceType}
    >
      {clientOutdated ? (
        <ClientOutdatedMessage />
      ) : confirmUseLogin ? (
        <ConfirmLoginUse
          confirm={async () => performLogin(confirmUseLogin.id, { confirmLogin: true })}
          label={getLoginDisplayLabel(confirmUseLogin) ?? t`Unnamed login`}
          loginLink={confirmUseLogin.loginUrl}
        />
      ) : addUsernameLogin ? (
        <UpdateLoginForm
          onSave={(username, password) => {
            dispatch({
              type: 'updateLoginAndFillForm',
              targetLoginId: addUsernameLogin.id,
              targetVaultId: addUsernameLogin.vaultMetadata?.id,
              username,
              password,
            });
            setNewLoginSuccess(true);
            setAddUsernameLoginId(undefined);
            addAchievement(Achievement.EXT_OVERLAY_UPDATE_GENERATED_PASSWORD);
          }}
          host={host}
          landscape={landscape}
          creationTime={addUsernameLogin.creationTime}
          password={addUsernameLogin.unprotectedPassword}
          archiveAddUsernameLogin={() => {
            sendMessage({ type: 'ArchiveAddUsernameLogin', loginId: addUsernameLogin.id }).catch(
              trackError,
            );
            setAddUsernameLoginId(undefined);
          }}
        />
      ) : subscriptionNagLogin ? (
        <SubscriptionNag
          subscriptionSettingsLink={subscriptionSettingsLink}
          organizationName={subscriptionNagLogin.organization.name}
          subscriptionError={subscriptionNagLogin.organization.subscriptionError}
          landscape={landscape}
        />
      ) : totpTimeOffsetWarningLoginId ? (
        <OverlayTotpWarning
          close={() => setTotpTimeOffsetWarningLoginId(undefined)}
          performLogin={() => performLogin(totpTimeOffsetWarningLoginId)}
          warningCleared={
            !iframeState.logins?.find((l) => l.id === totpTimeOffsetWarningLoginId)
              ?.showTotpTimeOffsetWarning
          }
          recheck={async () => {
            await sendMessage({ type: 'RequestSync' });
          }}
        />
      ) : (
        <OverlayLoginList
          logins={iframeState.logins ?? []}
          landscape={landscape}
          handleLoginClick={handleLoginClick}
          url={url}
          extrinsicHeight={extrinsicHeight}
          performDefaultActionRef={performDefaultActionRef}
          preferredLoginIds={iframeState.preferredLoginIds}
        />
      )}
    </LoginWindow>
  );
}

function useOverlayLayout() {
  const extrinsicSize = useExtrinsicIframeSize();
  const { width: extrinsicWidth, height: extrinsicHeight } = extrinsicSize;

  const landscape = useMemo(() => getLoginOverlayLandscape(extrinsicSize), [extrinsicSize]);

  return { landscape, extrinsicWidth, extrinsicHeight };
}
