import { AppliedOverrideInfo } from 'form-detection';
import { WebpageOverride } from 'persistable-types';

import { SelectorResults, Size } from 'src/types';

import { IframeStateDescriptor } from './types';

export type SelectElementState =
  | {
      state: 'inProgress';
      tagNames?: string[];
    }
  | {
      state: 'done';
      selector: string;
    };

export type OverrideCreateIframeStateDescriptorType = {
  mode: 'overrideCreate';
  state: {
    override?: WebpageOverride;
    intrinsicSize: Size;
    selectorResults: SelectorResults;
    hostname?: string;
    appliedInfo?: AppliedOverrideInfo;
    selectElementState?: SelectElementState;
    overrideIsReplace: boolean;
    align: 'left' | 'right';
  };
  action:
    | {
        type: 'setOverride';
        override?: WebpageOverride;
      }
    | {
        type: 'setIntrinsicSize';
        size: Size;
      }
    | {
        type: 'setSelectElementState';
        selectElementState?: SelectElementState;
      }
    | {
        type: 'setAlign';
        align: 'left' | 'right';
      };
};

export const overrideCreateIframeStateDescriptor: IframeStateDescriptor<OverrideCreateIframeStateDescriptorType> = {
  mode: 'overrideCreate',
  initialState: {
    override: undefined,
    intrinsicSize: { width: 0, height: 0 },
    selectorResults: {},
    overrideIsReplace: false,
    align: 'left',
  },
  reducer: (state, action) => {
    switch (action.type) {
      case 'setOverride':
        return { ...state, override: action.override };
      case 'setIntrinsicSize':
        return { ...state, intrinsicSize: action.size };
      case 'setSelectElementState':
        return { ...state, selectElementState: action.selectElementState };
      case 'setAlign':
        return { ...state, align: action.align };
    }
  },
};
