import { trackError } from 'backend-client-web';

import { useSelectorContext } from './SelectorContext';

export default function SelectorInput({
  selector,
  setSelector,
  disabled,
  tagNames,
}: {
  selector: string;
  setSelector: (value: string) => void;
  disabled?: boolean;
  tagNames?: string[];
}) {
  const { results, selectElement } = useSelectorContext();
  const selectorResult = typeof selector === 'string' ? results[selector] : undefined;
  return (
    <span>
      <input
        value={selector}
        onChange={(e) => setSelector(e.currentTarget.value)}
        disabled={disabled}
      />{' '}
      {selector === '' ? (
        <span title="Enter selector"></span>
      ) : selectorResult === undefined ? (
        <span title="Checking if present in page"></span>
      ) : selectorResult === 'found' ? (
        <span title="Present in page"></span>
      ) : selectorResult === 'notFound' ? (
        <span title="Not present in page"></span>
      ) : selectorResult === 'malformed' ? (
        <span title="Selector malformed"></span>
      ) : undefined}{' '}
      <button
        type="button"
        onClick={() => {
          (async () => {
            setSelector(await selectElement(tagNames));
          })().catch(trackError);
        }}
      >
        
      </button>
    </span>
  );
}
