import { Trans, t } from '@lingui/macro';
import classNames from 'classnames';
import { useCallback, useEffect, useMemo, useRef } from 'react';

import { VaultId } from 'backend-client-web';
import {
  HeyloginLogo,
  LinkButton,
  getFullLoginVaultLabel,
  getLoginVaultLabel,
  useElementSize,
} from 'client-web-sdk';

import { AUTOSNATCH_OVERLAY_SHADOW_WIDTH_PX } from 'src/constants';
import {
  AutosnatchState,
  AutosnatchedLogin,
} from 'src/iframeState/descriptors/autosnatchNotification';
import { GetAutosnatchTargetsResponse } from 'src/message';
import { Size } from 'src/types';

import AutosnatchTargetSelector from '../AutosnatchTargetSelector';

import styles from './AutosnatchNotificationOverlay.module.css';
import { ReactComponent as CrossIcon } from './cross.svg';

interface Props {
  login: AutosnatchedLogin;
  useFullVaultLabel: boolean;
  state: AutosnatchState;
  expanded: boolean;
  setExpanded: (expanded: boolean) => void;
  autosnatchTargetOptions: GetAutosnatchTargetsResponse;
  moveTargetInternal?: VaultId;
  setMoveTargetInternal: (target: VaultId | undefined) => void;
  autosnatchTarget: VaultId | undefined;
  setAutosnatchTarget: (autosnatchTarget: VaultId) => void;
  moveSelected: boolean;
  setMoveSelected: (moveSelected: boolean) => void;
  undo: () => void;
  setIntrinsicSize: (size: Size) => void;
  onClick: () => void;
  onMouseEnter: () => void;
  onMouseLeave: () => void;
  close: () => void;
  moveLogin: (target: VaultId) => void;
}

export default function AutosnatchNotificationOverlay({
  login,
  useFullVaultLabel,
  state,
  expanded,
  setExpanded,
  autosnatchTargetOptions,
  moveTargetInternal,
  setMoveTargetInternal,
  moveSelected,
  setMoveSelected,
  undo,
  close,
  setIntrinsicSize,
  onClick,
  onMouseEnter,
  onMouseLeave,
  moveLogin,
  autosnatchTarget,
  setAutosnatchTarget,
}: Props) {
  const ref = useRef<HTMLDivElement | null>(null);
  const [setSizeElement, size] = useElementSize<HTMLDivElement>();

  useEffect(() => {
    setIntrinsicSize(size);
  }, [size, setIntrinsicSize]);

  const multiplexedRef = useCallback(
    (el: HTMLDivElement | null) => {
      setSizeElement(el);
      ref.current = el;
    },
    [setSizeElement],
  );

  const headline = useMemo(() => {
    switch (state) {
      case 'snatched':
        return t`Login saved`;
      case 'snatchUndone':
        return t`Login not saved`;
      case 'passwordChanged':
        return t`Password changed`;
      case 'passwordChangeUndone':
        return t`Password not changed`;
      case 'loginMoved':
        return t`Login moved`;
    }
  }, [state]);

  return (
    <div
      className={styles.Wrapper}
      ref={multiplexedRef}
      style={
        {
          '--overlay-shadow-width': `${AUTOSNATCH_OVERLAY_SHADOW_WIDTH_PX}px`,
        } as React.CSSProperties
      }
    >
      <div
        className={classNames(styles.Overlay, {
          [styles.undone]: state === 'passwordChangeUndone' || state === 'snatchUndone',
          [styles.expanded]: expanded,
        })}
        onClick={() => {
          onClick();
          setExpanded(true);
        }}
        onMouseEnter={onMouseEnter}
        onMouseLeave={onMouseLeave}
      >
        <button className={styles.CloseButton} onClick={close}>
          <CrossIcon title={t`Close`} />
        </button>
        <header className={styles.Header}>
          <h2>{headline}</h2>
          <HeyloginLogo className={styles.Logo} />
        </header>
        <LoginInfo login={login} useFullVaultLabel={useFullVaultLabel} />
        {state === 'snatched' || state === 'loginMoved' ? (
          <SnatchActions
            login={login}
            expanded={expanded}
            autosnatchTargetOptions={autosnatchTargetOptions}
            setExpanded={setExpanded}
            undo={undo}
            close={close}
            moveTargetInternal={moveTargetInternal}
            setMoveTargetInternal={setMoveTargetInternal}
            moveSelected={moveSelected}
            setMoveSelected={setMoveSelected}
            moveLogin={moveLogin}
            autosnatchTarget={autosnatchTarget}
            setAutosnatchTarget={setAutosnatchTarget}
          />
        ) : state === 'passwordChanged' ? (
          <PasswordChangedActions
            login={login}
            expanded={expanded}
            setExpanded={setExpanded}
            undo={undo}
            close={close}
          />
        ) : null}
      </div>
    </div>
  );
}

function LoginInfo({
  login,
  useFullVaultLabel,
}: {
  login: AutosnatchedLogin;
  useFullVaultLabel: boolean;
}) {
  const vaultLabel = useMemo(() => {
    if (!login.vaultInfo) {
      return t`unknown location`;
    }

    return useFullVaultLabel
      ? getFullLoginVaultLabel(login.vaultInfo, login.organizationInfo ?? null)
      : getLoginVaultLabel(login.vaultInfo);
  }, [login.organizationInfo, login.vaultInfo, useFullVaultLabel]);

  return (
    <div className={styles.LoginInfo}>
      <div className={styles.Line}>
        {login.username ?? (
          <span>
            <CrossIcon className={styles.NoUsernameIcon} /> <Trans>Username unknown</Trans>
          </span>
        )}{' '}
      </div>
      <div className={styles.Line}>
        <Trans>
          <span className={styles.InLabel}>In</span> {vaultLabel}
        </Trans>
      </div>
    </div>
  );
}

interface SnatchActionsProps {
  expanded: boolean;
  setExpanded: (expanded: boolean) => void;
  login: AutosnatchedLogin;
  autosnatchTargetOptions: GetAutosnatchTargetsResponse;
  undo: () => void;
  close: () => void;
  moveTargetInternal?: VaultId;
  setMoveTargetInternal: (override: VaultId | undefined) => void;
  moveSelected: boolean;
  setMoveSelected: (moveSelected: boolean) => void;
  moveLogin: (target: VaultId) => void;
  autosnatchTarget: VaultId | undefined;
  setAutosnatchTarget: (autosnatchTarget: VaultId) => void;
}

function SnatchActions({
  login,
  undo,
  close,
  expanded,
  setExpanded,
  autosnatchTargetOptions,
  moveTargetInternal,
  setMoveTargetInternal,
  moveSelected,
  setMoveSelected,
  moveLogin,
  autosnatchTarget,
  setAutosnatchTarget,
}: SnatchActionsProps) {
  if (!expanded) {
    return (
      <button onClick={() => setExpanded(true)} className={styles.MoreOptionsButton}>
        <Trans>Click for more options</Trans>
      </button>
    );
  }

  if (autosnatchTargetOptions && moveSelected) {
    return (
      <div className={styles.Actions}>
        <div className={styles.TargetSelectorGroup}>
          <div className={styles.ToLabel}>
            <Trans>To</Trans>
          </div>
          <AutosnatchTargetSelector
            className={styles.TargetSelector}
            autosnatchTargetOverride={moveTargetInternal}
            autosnatchTargetOptions={autosnatchTargetOptions}
            autosnatchTarget={autosnatchTarget}
            setAutosnatchTarget={setAutosnatchTarget}
            setAutosnatchTargetOverride={setMoveTargetInternal}
            fixedSize
          />
        </div>
        <div className={styles.MoveButtonGroup}>
          <LinkButton className={styles.Action} onClick={() => setMoveSelected(false)}>
            <Trans>Back</Trans>
          </LinkButton>
          <LinkButton
            disabled={!moveTargetInternal || moveTargetInternal === login.vaultInfo?.id}
            className={styles.Action}
            onClick={() => {
              if (!moveTargetInternal) {
                return;
              }
              moveLogin(moveTargetInternal);
            }}
          >
            <Trans>Move</Trans>
          </LinkButton>
        </div>
      </div>
    );
  }

  return (
    <ul className={classNames(styles.Actions, styles.list)}>
      {!login.username && login.url && (
        <li>
          <a
            className={styles.Action}
            href={login.url}
            target="_blank"
            rel="noreferrer"
            onClick={close}
          >
            <Trans>Add username</Trans>
          </a>
        </li>
      )}
      {login.url && (
        <li>
          <a
            className={styles.Action}
            href={login.url}
            target="_blank"
            rel="noreferrer"
            onClick={close}
          >
            <Trans>Open in heylogin</Trans>
          </a>
        </li>
      )}
      {login.vaultInfo && autosnatchTargetOptions && (
        <li>
          <LinkButton className={styles.Action} type="button" onClick={() => setMoveSelected(true)}>
            <Trans>Move login</Trans>
          </LinkButton>
        </li>
      )}
      <li>
        <LinkButton className={styles.Action} onClick={undo}>
          <Trans>Do not save login</Trans>
        </LinkButton>
      </li>
    </ul>
  );
}

interface PasswordChangedActionsProps {
  expanded: boolean;
  setExpanded: (expanded: boolean) => void;
  login: AutosnatchedLogin;
  undo: () => void;
  close: () => void;
}

function PasswordChangedActions({
  expanded,
  setExpanded,
  login,
  undo,
  close,
}: PasswordChangedActionsProps) {
  if (!expanded) {
    return (
      <button onClick={() => setExpanded(true)} className={styles.MoreOptionsButton}>
        <Trans>Click for more options</Trans>
      </button>
    );
  }

  return (
    <ul className={classNames(styles.Actions, styles.list)}>
      <li>
        <a
          className={styles.Action}
          href={login.url}
          target="_blank"
          rel="noreferrer"
          onClick={close}
        >
          <Trans>Open in heylogin</Trans>
        </a>
      </li>
      <li>
        <LinkButton className={styles.Action} onClick={undo}>
          <Trans>Undo</Trans>
        </LinkButton>
      </li>
    </ul>
  );
}
