import { t, Trans } from '@lingui/macro';
import classNames from 'classnames';
import { useCallback, useEffect, useMemo, useRef } from 'react';

import { LoginId } from 'backend-client-web';
import {
  PushState,
  PushStateType,
  TotpCountdown,
  UnlockDeviceType,
  UnlockLabel,
  useElementSize,
  useTotp,
} from 'client-web-sdk';

import { TotpInputOverlayGeometry, TotpLogin } from 'src/iframeState/descriptors/totpInput';
import { ReactComponent as HeyloginIcon } from 'src/images/heylogin_icon.svg';
import { Size } from 'src/types';

import { ReactComponent as ArrowIcon } from './arrow-right.svg';
/* eslint css-modules/no-unused-class: [2, { markAsUsed: ['overlayInput', 'rightOfInput'] }] */
import styles from './TotpInputOverlay.module.css';

export type TotpInputOverlayType = 'overlayInput' | 'rightOfInput';

interface MenuProps {
  logins: TotpLogin[];
  selectLoginAndClose: (loginId: LoginId) => void;
  unlockDeviceType: UnlockDeviceType;
  pushState: PushStateType | null;
}

type Props = {
  expanded: boolean;
  setExpanded: (expanded: boolean) => void;
  setIntrinsicSize: (size: Size) => void;
  overlayGeometry: TotpInputOverlayGeometry;
} & MenuProps;

export default function TotpInputOverlay({
  setIntrinsicSize,
  expanded,
  setExpanded,
  overlayGeometry,
  ...menuProps
}: Props) {
  const ref = useRef<HTMLDivElement | null>(null);
  const [setSizeElement, size] = useElementSize<HTMLDivElement>();

  useEffect(() => {
    setIntrinsicSize(size);
  }, [size, setIntrinsicSize]);

  const multiplexedRef = useCallback(
    (el: HTMLDivElement | null) => {
      setSizeElement(el);
      ref.current = el;
    },
    [setSizeElement],
  );

  return (
    <div
      className={styles.Container}
      ref={multiplexedRef}
      onClick={(e) => {
        // Close when clicking in the empty space between menu and button. In these cases, container
        // itself is the click event's target.
        if (e.target === ref.current) {
          setExpanded(false);
        }
      }}
    >
      <button
        onClick={() => setExpanded(!expanded)}
        className={classNames(styles.Button, styles[overlayGeometry.type])}
        style={
          overlayGeometry.type === 'overlayInput'
            ? ({
                '--height': `${overlayGeometry.buttonHeight}px`,
              } as React.CSSProperties)
            : undefined
        }
      >
        <HeyloginIcon className={styles.HeyloginIcon} />
        {overlayGeometry.type === 'overlayInput' && <Trans>TOTP</Trans>}
      </button>
      {expanded && <Menu {...menuProps} />}
    </div>
  );
}

function Menu({ logins, selectLoginAndClose, pushState, unlockDeviceType }: MenuProps) {
  const loginsWithTotp = useMemo(() => logins.filter((l) => l.totpParameters), [logins]);

  if (pushState && pushState !== 'confirmed' && pushState !== 'connected') {
    return (
      <div className={styles.Menu}>
        <div className={styles.UnlockWrapper}>
          <PushState small={true} state={pushState} unlockDeviceType={unlockDeviceType} />
          {
            {
              connecting: <UnlockLabel unlockDeviceType={unlockDeviceType} />,
              error: t`Login cancelled`,
              warning: t`Failed`,
            }[pushState]
          }
        </div>
      </div>
    );
  }

  if (!loginsWithTotp.length) {
    // We might get logins with totpSecret === '' due to a bug in LoginEdit
    // We cannot distinguish them from proper totp secrets while locked so we display this placeholder
    // This should never happen though as the user should only see TOTP inputs if they have TOTP enabled in their login
    return (
      <div className={styles.Menu}>
        <div className={styles.EmptyListPlaceholder}>
          <Trans>You have no logins with TOTP on this page.</Trans>
        </div>
      </div>
    );
  }

  return (
    <ul className={styles.Menu}>
      {loginsWithTotp.map((login) => (
        <MenuListEntry login={login} key={login.id} selectLoginAndClose={selectLoginAndClose} />
      ))}
    </ul>
  );
}

function MenuListEntry({
  login,
  selectLoginAndClose,
}: {
  login: TotpLogin;
  selectLoginAndClose: (loginId: LoginId) => void;
}) {
  const { totp, totpPercent } = useTotp(
    login.totpParameters?.secret,
    login.totpParameters?.algorithm,
    login.totpParameters?.digits,
    login.totpParameters?.period,
  );

  return (
    <li className={styles.ListItem}>
      <button
        className={styles.ListItemButton}
        type="button"
        onClick={() => selectLoginAndClose(login.id)}
        disabled={!totp}
      >
        <div>
          <div className={styles.Label}>{login.displayName}</div>
          <div className={styles.TotpCode}>
            {totpPercent !== undefined && <TotpCountdown percent={totpPercent} />}
            {totp}
          </div>
        </div>
        <ArrowIcon className={styles.ArrowIcon} />
      </button>
    </li>
  );
}
