import { useMemo } from 'react';

import getHost from 'src/util/getHost';

export default function useParentPageUrl() {
  return useMemo(() => {
    const url = new URLSearchParams(window.location.search).get('url') ?? undefined;
    const host = url ? getHost(url) : undefined;
    return { url, host };
  }, []);
}
