import { Trans, Plural } from '@lingui/macro';
import classNames from 'classnames';
import React, { useEffect, useMemo, useRef, useState } from 'react';

import { getLoginDisplayLabel, LoginId, VaultType } from 'client-core';
import { useElementSize, LoginButton, ActionButton } from 'client-web-sdk';

import useFormatDateRelative from 'src/entrypoints/iframe/utils/useFormatDateRelative';
import { ContentLogin } from 'src/types';
import getDisambiguatedLogins from 'src/util/getDisambiguatedLogins';

import styles from './OverlayLoginList.module.css';
import OverlayLoginSearch from './OverlayLoginSearch';

interface Props {
  logins: readonly ContentLogin[];
  handleLoginClick: (login: ContentLogin) => void;
  landscape: boolean;
  url: string | undefined;
  extrinsicHeight: number;
  performDefaultActionRef?: React.MutableRefObject<() => void>;
  preferredLoginIds?: LoginId[];
}

export default function OverlayLoginList({
  logins,
  landscape,
  handleLoginClick,
  url,
  extrinsicHeight,
  performDefaultActionRef,
  preferredLoginIds,
}: Props) {
  const [hideInferiorMatches, setHideInferiorMatches] = useState(true);

  const disambiguatedLogins = useMemo(() => getDisambiguatedLogins(logins, url), [logins, url]);
  const bestMatchLogins = useMemo(() => {
    const preferredLogins =
      preferredLoginIds && disambiguatedLogins.filter(({ id }) => preferredLoginIds.includes(id));
    const loginsToProcess = preferredLogins?.length ? preferredLogins : disambiguatedLogins;
    const index = loginsToProcess.findIndex(
      (l) => l.urlSimilarity < loginsToProcess[0].urlSimilarity,
    );
    return index === -1 ? loginsToProcess : loginsToProcess.slice(0, index);
  }, [disambiguatedLogins, preferredLoginIds]);

  const displayedLogins = hideInferiorMatches ? bestMatchLogins : disambiguatedLogins;

  const hiddenLoginsCount = disambiguatedLogins.length - displayedLogins.length;
  const loginsHidden = hiddenLoginsCount > 0;

  const horizontal = landscape && displayedLogins.length < (loginsHidden ? 2 : 3);

  const [loginItemRef, { height: loginItemHeight }] = useElementSize<HTMLLIElement>();

  const maxLoginItemCount = useMemo(() => {
    if (loginItemHeight === 0 || horizontal) {
      return Infinity;
    }
    const availableHeight = extrinsicHeight;
    // subtract 1 to allow for some padding in overlay window
    const loginsFittingInHeight = Math.floor(availableHeight / loginItemHeight) - 1;
    // Always show at least 3 items (2 logins + search or 3 logins)
    return Math.max(loginsFittingInHeight, 3);
  }, [extrinsicHeight, horizontal, loginItemHeight]);
  const showSearch =
    displayedLogins.length > (loginsHidden ? maxLoginItemCount - 1 : maxLoginItemCount);
  const listLogins = showSearch ? displayedLogins.slice(0, maxLoginItemCount - 1) : displayedLogins;

  // Hack to force a re-render after loginItemRef has been set. The ref is only initialized once the
  // list has been rendered, but we need to re-render with it to decide whether to show the search.
  const [, setListLoginLength] = useState<number>();
  useEffect(() => {
    setListLoginLength(listLogins.length);
  }, [listLogins.length]);

  const searchMenuHeight = loginItemHeight * listLogins.length;

  const listRef = useRef<HTMLUListElement | null>(null);
  const searchItemRef = useRef<HTMLLIElement | null>(null);
  useArrowFocus(listRef, searchItemRef);

  useEffect(() => {
    // Default action is passed down to OverlayLoginSearch if that is shown
    if (!performDefaultActionRef || showSearch) {
      return;
    }
    if (listLogins.length === 1) {
      performDefaultActionRef.current = () => handleLoginClick(listLogins[0]);
    } else {
      performDefaultActionRef.current = () => listRef.current?.querySelector('button')?.focus();
    }
  }, [handleLoginClick, listLogins, listLogins.length, performDefaultActionRef, showSearch]);

  return (
    <ul
      className={classNames(styles.LoginList, {
        [styles.horizontal]: horizontal,
      })}
      ref={listRef}
    >
      {showSearch && (
        <li key="search" className={styles.LoginListItem} ref={searchItemRef}>
          <OverlayLoginSearch
            // Always show all matches in search
            logins={disambiguatedLogins}
            size={landscape ? 'small' : 'medium'}
            handleLoginClick={handleLoginClick}
            menuHeight={searchMenuHeight}
            performDefaultActionRef={performDefaultActionRef}
          />
        </li>
      )}
      {listLogins.map((l, i) => (
        // Use one of the li's to measure the login item height. We don't use the 0th on, since it
        // doesn't have margin in all situations.
        <li key={i} className={styles.LoginListItem} ref={i === 1 ? loginItemRef : undefined}>
          <LoginListItem onClick={() => handleLoginClick(l)} login={l} landscape={landscape} />
        </li>
      ))}
      {hiddenLoginsCount > 0 && !showSearch && (
        <li key="showAll" className={styles.LoginListItem}>
          <ActionButton
            buttonStyle="tertiary"
            type="button"
            onClick={() => setHideInferiorMatches(false)}
            className={styles.ShowAllButton}
          >
            <Plural value={hiddenLoginsCount} one="Show # more login" other="Show # more logins" />
          </ActionButton>
        </li>
      )}
    </ul>
  );
}

function LoginListItem({
  onClick,
  login,
  landscape,
}: {
  login: ContentLogin & { website?: string };
  landscape: boolean;
  onClick: () => void;
}) {
  const relativeDate = useFormatDateRelative(login.creationTime);

  const displayLabel = getLoginDisplayLabel(login);
  return (
    <LoginButton
      onClick={onClick}
      size={landscape ? 'small' : 'medium'}
      className={styles.LoginButton}
      icon={
        login.vaultMetadata?.type === VaultType.TEAM
          ? 'team'
          : login.vaultMetadata?.type === VaultType.INBOX
          ? 'inbox'
          : undefined
      }
    >
      {displayLabel ? (
        <>
          <div>{displayLabel}</div>
          {login.website && (
            <div className={styles.LoginButtonSubtitle}>
              <Trans>on {login.website}</Trans>
            </div>
          )}
        </>
      ) : (
        <>
          <div>
            <Trans>Unnamed login</Trans>
          </div>
          <div className={styles.LoginButtonSubtitle}>
            <Trans>Login created {relativeDate}</Trans>
          </div>
        </>
      )}
    </LoginButton>
  );
}

function useArrowFocus(
  listRef: React.MutableRefObject<HTMLUListElement | null>,
  searchItemRef: React.MutableRefObject<HTMLLIElement | null>,
) {
  useEffect(() => {
    function onKeyDown(e: KeyboardEvent) {
      if (e.metaKey || e.ctrlKey || e.altKey || e.shiftKey || e.defaultPrevented) {
        return;
      }

      let direction;
      if (e.key === 'ArrowUp') {
        direction = -1;
      } else if (e.key === 'ArrowDown') {
        direction = 1;
      } else {
        return;
      }

      const focused = document.querySelector<HTMLElement>(':focus');
      if (
        !focused ||
        !listRef.current?.contains(focused) ||
        searchItemRef.current?.contains(focused)
      ) {
        return;
      }
      const focusable: HTMLElement[] = [
        ...listRef.current.querySelectorAll('button, input'),
      ].filter((f): f is HTMLElement => f instanceof HTMLElement);
      const index = focusable.indexOf(focused);
      if (index === -1) {
        return;
      }
      const newIndex = (index + direction + focusable.length) % focusable.length;
      focusable[newIndex].focus();
    }
    document.addEventListener('keydown', onKeyDown);
    return () => document.removeEventListener('keydown', onKeyDown);
  });
}
