import { useCallback } from 'react';

import { Achievement, VaultId, trackError } from 'backend-client-web';

import { autosnatchIframeStateDescriptor } from 'src/iframeState/descriptors/autosnatch';
import useDispatchSetter from 'src/iframeState/hooks/useDispatchSetter';
import useIframeState from 'src/iframeState/hooks/useIframeState';
import { sendMessage } from 'src/message';
import { Size } from 'src/types';
import useAutosnatchSettings from 'src/util/react/useAutosnatchSettings';
import useAutosnatchTargetOverride from 'src/util/react/useAutosnatchTargetOverride';
import useCloseOnEscape from 'src/util/react/useCloseOnEscape';

import AutosnatchOverlay from '../components/autosnatchOverlay/AutosnatchOverlay';
import useExtrinsicIframeSize from '../utils/getExtrinsicSizeFromLocation';

import useContentScriptConnection from './useContentScriptConnection';

export default function AutosnatchOverlayContainer() {
  const port = useContentScriptConnection();

  const { iframeState, dispatch } = useIframeState(port, autosnatchIframeStateDescriptor);

  const setExpanded = useDispatchSetter(dispatch, (expanded: boolean) => ({
    type: 'setExpanded',
    expanded,
  }));

  const setVisible = useDispatchSetter(dispatch, (visible: boolean) => ({
    type: 'setVisible',
    visible,
  }));

  const setIntrinsicSize = useDispatchSetter(dispatch, (size: Size) => ({
    type: 'setIntrinsicSize',
    size,
  }));

  const setAutosnatchTargetOverride = useDispatchSetter(
    dispatch,
    (autosnatchTargetOverride: VaultId | undefined) => ({
      type: 'setAutosnatchTargetOverride',
      autosnatchTargetOverride,
    }),
  );
  const { height: passwordFieldHeight } = useExtrinsicIframeSize();

  const handleResize = useCallback(
    (width: number, height: number) => {
      setIntrinsicSize({ width, height });
    },
    [setIntrinsicSize],
  );

  const openPasswordGenerator = useCallback(() => {
    dispatch({ type: 'showPasswordGenerator' });
  }, [dispatch]);

  const showLoginOverlay = useCallback(() => {
    dispatch({ type: 'showLoginOverlay' });
  }, [dispatch]);

  const close = useCallback(() => {
    setVisible(false);
  }, [setVisible]);

  useCloseOnEscape(() => setExpanded(false));

  const {
    autosnatchTarget,
    setAutosnatchTarget,
    autosnatchDisabled,
    setAutosnatchDisabled,
  } = useAutosnatchSettings();

  const setAutosnatchDisabledWithAchievement = useCallback(
    (disabled: boolean) => {
      setAutosnatchDisabled(disabled);
      sendMessage({
        type: 'AddAchievements',
        achievements: [Achievement.EXT_OVERLAY_DISABLE_AUTOSNATCH],
      }).catch(trackError);
    },
    [setAutosnatchDisabled],
  );

  const overrideProps = useAutosnatchTargetOverride(
    iframeState.autosnatchTargetOverride,
    setAutosnatchTargetOverride,
  );

  return (
    <AutosnatchOverlay
      autosnatchTarget={autosnatchTarget}
      setAutosnatchTarget={setAutosnatchTarget}
      onResize={handleResize}
      autosnatchActive={!autosnatchDisabled}
      expanded={iframeState.expanded}
      setAutosnatchActive={(v) => setAutosnatchDisabledWithAchievement(!v)}
      setExpanded={setExpanded}
      openPasswordGenerator={openPasswordGenerator}
      passwordFieldHeight={passwordFieldHeight}
      close={close}
      pageHasLogins={iframeState.pageHasLogins}
      showLoginOverlay={showLoginOverlay}
      {...overrideProps}
    />
  );
}
