import { Fragment, useCallback, useEffect, useMemo, useState } from 'react';
import { debounce } from 'throttle-debounce';

import { UUID } from 'backend-client-web';
import { useElementSize } from 'client-web-sdk';
import { AppliedOverrideInfo } from 'form-detection';
import { newUuid, OverrideRule, WebpageOverride, WebpageOverrideStatus } from 'persistable-types';

import { SelectElementState } from 'src/iframeState/descriptors/overrideCreate';
import { SelectorResults, Size } from 'src/types';
import useErrorHandlingCallback from 'src/util/react/useErrorHandlingCallback';

import styles from './OverrideCreateOverlay.module.css';
import PredicateEdit from './PredicateEdit';
import RuleCreate from './RuleCreate';
import RuleEdit from './RuleEdit';
import { SelectorContextProvider } from './SelectorContext';

interface Props {
  appliedInfo?: AppliedOverrideInfo;
  hostname: string | undefined;
  override: WebpageOverride | undefined;
  selectElementState: SelectElementState | undefined;
  selectorResults: SelectorResults;
  overrideIsReplace: boolean;
  setIntrinsicSize: (size: Size) => void;
  setOverride: (newOverride: WebpageOverride | undefined) => void;
  setSelectElementState: (s: SelectElementState | undefined) => void;
  deleteOverride: (overrideId: UUID) => Promise<void>;
  replaceOverride: (override: WebpageOverride) => Promise<void>;
  setAlign: (align: 'left' | 'right') => void;
  align: 'left' | 'right';
}

export default function OverrideCreateOverlay({
  appliedInfo,
  hostname,
  override,
  selectElementState,
  selectorResults,
  overrideIsReplace,
  setIntrinsicSize,
  setOverride,
  setSelectElementState,
  deleteOverride,
  replaceOverride,
  setAlign,
  align,
}: Props) {
  const overrideOrDefault = useMemo(
    () =>
      override ?? {
        id: newUuid(),
        status: WebpageOverrideStatus.hotfix,
        hostnameContains: hostname ?? '',
        rules: [],
      },
    [hostname, override],
  );

  const hasHostnameContains =
    'hostnameContains' in overrideOrDefault &&
    typeof overrideOrDefault.hostnameContains === 'string';

  const addRuleAtIndex = useCallback(
    (rule: OverrideRule, index: number) => {
      const rules = [...overrideOrDefault.rules];
      rules.splice(index, 0, rule);
      setOverride({ ...overrideOrDefault, rules });
    },
    [overrideOrDefault, setOverride],
  );

  const [rootRef, size] = useElementSize();
  useEffect(() => {
    if (size.width > 0 && size.height > 0) {
      setIntrinsicSize(size);
    }
  }, [setIntrinsicSize, size]);

  const handleReplace = useErrorHandlingCallback(async () => {
    if (!override) {
      return;
    }
    await replaceOverride(override);
    setOverride(undefined);
  }, [override, replaceOverride, setOverride]);

  const handleDelete = useErrorHandlingCallback(async () => {
    if (!override) {
      return;
    }
    await deleteOverride(override.id);
    setOverride(undefined);
  }, [override, deleteOverride, setOverride]);

  return (
    <SelectorContextProvider
      selectorResults={selectorResults}
      selectElementState={selectElementState}
      setSelectElementState={setSelectElementState}
    >
      <main ref={rootRef} className={styles.OverrideCreateOverlay}>
        <section>
          <h3>
            {overrideIsReplace ? 'Edit override' : 'Create override'}{' '}
            <button onClick={() => setAlign(align === 'left' ? 'right' : 'left')}>
              {align === 'left' ? 'Move overlay right' : 'Move overlay left'}
            </button>
          </h3>
          <p>
            <button type="button" disabled={!override} onClick={handleReplace}>
              {overrideIsReplace ? 'Replace existing override' : 'Upload new override'}
            </button>{' '}
            <button type="button" disabled={!override || !overrideIsReplace} onClick={handleDelete}>
              Delete override
            </button>{' '}
            <button
              type="button"
              disabled={!override}
              onClick={() => {
                if (
                  // eslint-disable-next-line no-restricted-globals
                  confirm('The override in creation will be discarded. Continue?')
                ) {
                  setOverride(undefined);
                }
              }}
            >
              {overrideIsReplace ? 'Discard changes' : 'Discard new override'}
            </button>
          </p>
          {override && (
            <p>
              {overrideIsReplace ? (
                <>
                  Replacing override with id <code>{override.id}</code>
                </>
              ) : (
                <>Creating new override</>
              )}
            </p>
          )}
          <p>
            {appliedInfo?.override.id === overrideOrDefault?.id ? (
              <strong> This override is active</strong>
            ) : appliedInfo ? (
              <span>
                 Another override is active{' '}
                <button
                  type="button"
                  onClick={() => {
                    if (
                      !override ||
                      // eslint-disable-next-line no-restricted-globals
                      confirm("This will discard the override you're currently creating/editing.")
                    ) {
                      setOverride(appliedInfo.override);
                    }
                  }}
                >
                  Edit
                </button>
                <br />
                Id: <code>{appliedInfo.override.id}</code>
                {'hostnameContains' in appliedInfo.override && (
                  <>
                    {' '}
                    , hostnameContains: <code>{appliedInfo.override.hostnameContains}</code>
                  </>
                )}
              </span>
            ) : (
              <span> No override active</span>
            )}
          </p>
        </section>
        <section>
          <h4>Override properties</h4>
          <p>
            <label>
              <input
                type="checkbox"
                checked={overrideOrDefault.status === WebpageOverrideStatus.hotfix}
                onChange={(e) =>
                  setOverride({
                    ...overrideOrDefault,
                    status: e.currentTarget.checked ? WebpageOverrideStatus.hotfix : undefined,
                  })
                }
              />{' '}
              Hotfix
            </label>
          </p>
          <p>
            <label>
              <input
                type="checkbox"
                checked={overrideOrDefault.comment !== undefined}
                onChange={(e) =>
                  setOverride({
                    ...overrideOrDefault,
                    comment: e.currentTarget.checked ? '' : undefined,
                  })
                }
              />{' '}
              Comment
            </label>
            <CommentEdit setOverride={setOverride} overrideOrDefault={overrideOrDefault} />
          </p>
        </section>
        <section>
          <h4>Webpage Conditions</h4>
          <p>
            <em>Either a hostname contains or a predicate must be present.</em>
          </p>
          <div>
            <label>
              <input
                type="checkbox"
                checked={hasHostnameContains}
                onChange={(e) => {
                  setOverride(
                    e.currentTarget.checked
                      ? {
                          ...overrideOrDefault,
                          hostnameContains:
                            hasHostnameContains && 'hostnameContains' in overrideOrDefault
                              ? overrideOrDefault.hostnameContains
                              : hostname ?? '',
                        }
                      : withDeleteProperty(
                          {
                            ...overrideOrDefault,
                            predicate: overrideOrDefault.predicate ?? {
                              type: 'elementExists',
                              selector: '',
                            },
                          },
                          'hostnameContains',
                        ),
                  );
                }}
              />{' '}
              Filter by hostname contains
            </label>
            :{' '}
            <input
              type="text"
              value={
                hasHostnameContains && 'hostnameContains' in overrideOrDefault
                  ? overrideOrDefault.hostnameContains
                  : ''
              }
              onChange={(e) =>
                setOverride({ ...overrideOrDefault, hostnameContains: e.currentTarget.value })
              }
              disabled={!hasHostnameContains}
              placeholder="example.org"
            />
          </div>
          <PredicateEdit
            predicate={overrideOrDefault.predicate}
            setPredicate={(p) =>
              setOverride(
                p
                  ? { ...overrideOrDefault, predicate: p }
                  : withDeleteProperty(
                      {
                        ...overrideOrDefault,
                        hostnameContains:
                          'hostnameContains' in overrideOrDefault
                            ? overrideOrDefault.hostnameContains
                            : '',
                      },
                      'predicate',
                    ),
              )
            }
          />
        </section>
        <section>
          <h4>Rules</h4>
          <p>
            <em>Rules are evaluated from top to bottom.</em>
          </p>
          {overrideOrDefault.rules.map((rule, i) => (
            <Fragment key={i}>
              <RuleCreate addRule={(r) => addRuleAtIndex(r, i)} />
              <RuleEdit
                rule={rule}
                applied={
                  appliedInfo?.override.id === overrideOrDefault.id &&
                  appliedInfo.ruleIndexes.includes(i)
                }
                setRule={(r) => {
                  const rules = [...overrideOrDefault.rules];
                  rules.splice(i, 1, r);
                  setOverride({
                    ...overrideOrDefault,
                    rules,
                  });
                }}
                deleteRule={() => {
                  const rules = [...overrideOrDefault.rules];
                  rules.splice(i, 1);
                  setOverride({
                    ...overrideOrDefault,
                    rules,
                  });
                }}
              />
            </Fragment>
          ))}
          <RuleCreate addRule={(r) => addRuleAtIndex(r, overrideOrDefault.rules.length)} />
        </section>
      </main>
    </SelectorContextProvider>
  );
}

function CommentEdit({
  setOverride,
  overrideOrDefault,
}: {
  overrideOrDefault: WebpageOverride;
  setOverride: (newOverride: WebpageOverride | undefined) => void;
}) {
  const [commentInEdit, setCommentInEdit] = useState<string>();

  const updateCommentDebounced = useMemo(
    () =>
      debounce(100, (comment: string) => {
        setOverride({ ...overrideOrDefault, comment });
      }),
    [overrideOrDefault, setOverride],
  );

  if (overrideOrDefault.comment === undefined) {
    return null;
  }

  return (
    <textarea
      value={commentInEdit ?? overrideOrDefault.comment}
      onChange={(e) => {
        const comment = e.currentTarget.value;
        setCommentInEdit(comment);
        updateCommentDebounced(comment);
      }}
      onFocus={() => {
        setCommentInEdit(overrideOrDefault.comment);
      }}
      onBlur={() => {
        if (commentInEdit !== undefined) {
          updateCommentDebounced(commentInEdit);
        }
        setCommentInEdit(undefined);
      }}
    />
  );
}

function withDeleteProperty<O extends object, K extends string | number | symbol>(
  obj: O,
  k: K,
): Omit<O, K> {
  const newObj = { ...obj };
  delete newObj[(k as unknown) as keyof O];
  return newObj;
}
