import { VaultId } from 'backend-client-web';
import { ISODateString, LoginVaultMetadata, OrganizationMetadata } from 'client-core';

import { AutosnatchId, Size } from 'src/types';

import { IframeStateDescriptor } from './types';

export type AutosnatchState =
  | 'snatched'
  | 'snatchUndone'
  | 'passwordChanged'
  | 'passwordChangeUndone'
  | 'loginMoved';

export interface AutosnatchNotificationUiState {
  state: AutosnatchState;
  expanded: boolean;
  login: AutosnatchedLogin;
  useFullVaultLabel: boolean;
  moveTargetInternal?: VaultId;
  moveSelected: boolean;
  entryAnimationShown: boolean;
}

export interface AutosnatchedLogin {
  username?: string;
  vaultInfo?: Pick<LoginVaultMetadata, 'type' | 'name' | 'id'>;
  organizationInfo?: Pick<OrganizationMetadata, 'name'>;
  url?: string;
}

export type AutosnatchNotificationIframeState = {
  intrinsicSize: Size;
  autosnatchInfo?: { id: AutosnatchId; performedAt: ISODateString };
  uiState?: AutosnatchNotificationUiState;
};

export type AutosnatchNotificationIframeStateDescriptorType = {
  mode: 'autosnatchNotification';
  state: AutosnatchNotificationIframeState;
  action:
    | { type: 'setIntrinsicSize'; size: Size }
    | { type: 'undo' }
    | { type: 'close' }
    | { type: 'setExpanded'; expanded: boolean }
    | { type: 'moveLogin'; targetVaultId: string }
    | { type: 'setMoveTargetInternal'; moveTargetInternal?: string }
    | { type: 'setMoveSelected'; moveSelected: boolean };
};

export const autosnatchNotificationIframeStateDescriptor: IframeStateDescriptor<AutosnatchNotificationIframeStateDescriptorType> = {
  mode: 'autosnatchNotification',
  initialState: {
    intrinsicSize: { width: 0, height: 0 },
    autosnatchInfo: undefined,
    uiState: undefined,
  },
  reducer: (state, action) => {
    switch (action.type) {
      case 'setIntrinsicSize':
        return { ...state, intrinsicSize: action.size };
      case 'undo':
        return state;
      case 'close':
        return state;
      case 'moveLogin':
        return state;
      case 'setExpanded':
        return {
          ...state,
          uiState: state.uiState ? { ...state.uiState, expanded: action.expanded } : undefined,
        };
      case 'setMoveSelected':
        return {
          ...state,
          uiState: state.uiState
            ? { ...state.uiState, moveSelected: action.moveSelected }
            : undefined,
        };
      case 'setMoveTargetInternal':
        return {
          ...state,
          uiState: state.uiState
            ? { ...state.uiState, moveTargetInternal: action.moveTargetInternal }
            : undefined,
        };
    }
  },
};
