import { VaultId } from 'backend-client-web';

import { Size } from 'src/types';

import { IframeStateDescriptor } from './types';

export type PasswordGeneratorIframeStateDescriptorType = {
  mode: 'passwordGenerator';
  state: {
    intrinsicSize: Size;
    hidden: boolean;
    autosnatchTargetOverride?: VaultId;
    generatedPassword?: string;
    expanded: boolean;
    passwordInsertedTimestampMs?: number;
    insertInitialPassword: boolean;
  };
  action:
    | { type: 'setIntrinsicSize'; size: Size }
    | { type: 'setHidden'; hidden: boolean }
    | { type: 'setAutosnatchTargetOverride'; autosnatchTargetOverride?: VaultId }
    | { type: 'setGeneratedPassword'; generatedPassword?: string }
    | { type: 'setExpanded'; expanded: boolean }
    | { type: 'writeToClipboard'; text: string; clearAfterTimeout: boolean };
};

export const passwordGeneratorIframeStateDescriptor: IframeStateDescriptor<PasswordGeneratorIframeStateDescriptorType> = {
  mode: 'passwordGenerator',
  initialState: {
    hidden: false,
    intrinsicSize: { width: 0, height: 0 },
    expanded: false,
    insertInitialPassword: false,
  },
  reducer: (state, action) => {
    switch (action.type) {
      case 'setIntrinsicSize':
        return { ...state, intrinsicSize: action.size };
      case 'setHidden':
        return { ...state, hidden: action.hidden };
      case 'setAutosnatchTargetOverride':
        return { ...state, autosnatchTargetOverride: action.autosnatchTargetOverride };
      case 'setGeneratedPassword':
        return { ...state, generatedPassword: action.generatedPassword };
      case 'setExpanded':
        return { ...state, expanded: action.expanded };
      case 'writeToClipboard':
        return state;
    }
  },
};
