import deepEqual from 'deep-equal';
import { Dispatch, SetStateAction, useCallback, useEffect, useMemo, useRef, useState } from 'react';
import { debounce } from 'throttle-debounce';

import { debugConsole } from 'backend-client-web';

import { ContentToIframeMessage, ForwardIframeClientPort, StateUpdate } from 'src/connections';
import { IframeMode, IframeStateDescriptorType } from 'src/iframeState/descriptors';
import { IframeStateDescriptor } from 'src/iframeState/descriptors/types';

export default function useIframeState<D extends IframeStateDescriptorType>(
  port: ForwardIframeClientPort | undefined,
  descriptor: IframeStateDescriptor<D>,
) {
  const [iframeState, setIframeState] = useStateWithReferentialIdentityPreservation<D['state']>(
    descriptor.initialState,
  );

  useEffect(() => {
    if (!port) {
      return undefined;
    }

    function handleMessage(msg: ContentToIframeMessage) {
      if (isStateUpdateMessage(msg, descriptor.mode)) {
        const newState = msg.state;
        debugConsole.log('[useIframeState]', descriptor.mode, 'StateUpdate', newState);
        setIframeState(newState);
      }
    }

    port.postMessage({ type: 'RequestState', iframeMode: descriptor.mode });

    port.onMessage.addListener(handleMessage);
  }, [descriptor.mode, port, setIframeState]);

  const actionQueueRef = useRef<D['action'][]>([]);

  const processActionQueueDebounced = useMemo(
    () =>
      debounce(20, (p: ForwardIframeClientPort) => {
        if (!actionQueueRef.current.length) {
          return;
        }
        p.postMessage({
          type: 'DispatchStateActions',
          iframeMode: descriptor.mode,
          actions: actionQueueRef.current,
        });
        actionQueueRef.current = [];
      }),
    [descriptor.mode],
  );

  const dispatch = useCallback(
    (...actions: D['action'][]) => {
      // Optimistically apply the action immediately. We're going to get a state update from the
      // canonical source in the content script soon after we send the message, but in the meantime
      // we'd still be operating with the old values. In particular this messes with react keeping
      // track of cursor positions in text inputs. The recommended fix is to update synchronously.
      // See https://github.com/facebook/react/issues/955#issuecomment-469352730
      debugConsole.log('[useIframeState]', descriptor.mode, 'dispatch', ...actions);
      setIframeState((oldState) => actions.reduce(descriptor.reducer, oldState));
      // send actions to content script with a very slight debounce to minimize update noise
      actionQueueRef.current.push(...actions);
      if (port) {
        processActionQueueDebounced(port);
      } else {
        debugConsole.warn(
          '[useIframeState]',
          descriptor.mode,
          'dispatch',
          'not scheduling action processing since port is not yet set',
        );
      }
    },
    [descriptor.mode, descriptor.reducer, port, processActionQueueDebounced, setIframeState],
  );

  // We skip processing above if port is not set, so we need to enqueue is anew if port changed.
  const oldPortRef = useRef(port);
  useEffect(() => {
    if (oldPortRef.current === port) {
      return;
    }
    oldPortRef.current = port;
    if (port) {
      processActionQueueDebounced(port);
    }
  }, [port, processActionQueueDebounced]);

  return { iframeState, dispatch };
}

function useStateWithReferentialIdentityPreservation<S>(
  initialState: S | (() => S),
): [S, Dispatch<SetStateAction<S>>] {
  const [state, setState] = useState<S>(initialState);
  const setStateIfNotDeepEquals: React.Dispatch<React.SetStateAction<S>> = useCallback(
    (setStateAction: React.SetStateAction<S>) => {
      setState((oldState) => {
        const newState =
          typeof setStateAction == 'function'
            ? (setStateAction as (prevState: S) => S)(oldState)
            : setStateAction;
        // try to keep referential integrity of state intact by reusing previous state if deep equal
        if (deepEqual(oldState, newState)) {
          return oldState;
        }
        return newState;
      });
    },
    [],
  );
  return [state, setStateIfNotDeepEquals];
}

function isStateUpdateMessage<M extends IframeMode>(
  msg: ContentToIframeMessage,
  iframeMode: M,
): msg is StateUpdate<M> {
  if (msg.type !== 'StateUpdate') {
    return false;
  }
  checkIframeMode(msg, iframeMode);
  return true;
}

function checkIframeMode<M extends IframeMode>(msg: { iframeMode: IframeMode }, iframeMode: M) {
  if (msg.iframeMode !== iframeMode) {
    throw new Error(
      `Received incorrect iframe mode message! Expected: ${iframeMode}, got: ${msg.iframeMode}`,
    );
  }
}
