import { t, Trans } from '@lingui/macro';
import classNames from 'classnames';
import { useCombobox } from 'downshift';
import React, { useEffect, useRef, useState } from 'react';

import { searchPredicate, VaultType, getLoginDisplayLabel } from 'client-core';

import { ReactComponent as ForwardIcon } from 'src/images/forward.svg';
import { ReactComponent as SearchIcon } from 'src/images/search.svg';
import { ReactComponent as TeamIcon } from 'src/images/team.svg';
import { ContentLogin } from 'src/types';

import useFormatDateRelative from '../utils/useFormatDateRelative';

/* eslint css-modules/no-unused-class: [2, { markAsUsed: ['medium', 'small'] }] */
import styles from './OverlayLoginSearch.module.css';

interface Props {
  logins: readonly (ContentLogin & { website?: string })[];
  size?: 'medium' | 'small';
  menuHeight: number;
  handleLoginClick: (login: ContentLogin) => void;
  performDefaultActionRef?: React.MutableRefObject<() => void>;
}

export default function OverlayLoginSearch({
  logins,
  size,
  menuHeight,
  handleLoginClick,
  performDefaultActionRef,
}: Props) {
  const [filteredLogins, setFilteredLogins] = useState(logins);
  const {
    isOpen,
    highlightedIndex,
    inputValue,
    getLabelProps,
    getMenuProps,
    getInputProps,
    getComboboxProps,
    getItemProps,
    openMenu,
  } = useCombobox({
    itemToString: (item) => (item ? getLoginDisplayLabel(item) ?? '' : ''),
    // @ts-ignore
    items: filteredLogins,
    onSelectedItemChange: ({ selectedItem }) => {
      if (selectedItem) {
        handleLoginClick(selectedItem);
      }
    },
  });

  useEffect(() => {
    setFilteredLogins(
      !inputValue ? logins : logins.filter((item) => searchPredicate(item, inputValue)),
    );
  }, [inputValue, logins]);

  const inputRef = useRef<HTMLInputElement | null>(null);
  useEffect(() => {
    if (performDefaultActionRef) {
      performDefaultActionRef.current = () => {
        inputRef.current?.focus();
      };
    }
  }, [performDefaultActionRef]);

  return (
    <div
      className={classNames(styles.OverlayLoginSearch, styles[size ?? 'medium'], {
        [styles.open]: isOpen,
      })}
    >
      <div {...getComboboxProps()} className={styles.InputWrapper}>
        <label
          {...getLabelProps()}
          className={classNames(styles.Placeholder, {
            [styles.visuallyhidden]: inputValue !== '',
          })}
        >
          <SearchIcon title="" className={styles.PlaceholderIcon} />
          <span className={styles.PlaceholderText}>
            <Trans>Search {logins.length} logins</Trans>
          </span>
        </label>
        <input
          {...getInputProps({
            onFocus: () => {
              if (!isOpen) {
                openMenu();
              }
            },
            ref: inputRef,
          })}
          className={styles.Input}
        />
      </div>
      <div
        className={classNames(styles.MenuWrapper, {
          [styles.visuallyhidden]: !isOpen,
        })}
      >
        <ul
          {...getMenuProps()}
          className={styles.Menu}
          style={{ '--menu-height': `${menuHeight}px` }}
        >
          {isOpen && filteredLogins.length ? (
            filteredLogins.map((login, index) => (
              <li
                className={classNames(styles.MenuItem, {
                  [styles.highlighted]: highlightedIndex === index,
                })}
                key={login.id}
                {...getItemProps({ item: login, index })}
              >
                {login.vaultMetadata?.type === VaultType.TEAM && <TeamIcon title={t`Team`} />}
                <MenuItemLabel login={login} />
                <ForwardIcon title="" />
              </li>
            ))
          ) : (
            <li className={styles.NothingFoundMessage}>
              <Trans>No logins found</Trans>
            </li>
          )}
        </ul>
      </div>
    </div>
  );
}

function MenuItemLabel({ login }: { login: ContentLogin & { website?: string } }) {
  const displayLabel = getLoginDisplayLabel(login);
  const dateString = login.creationTime;
  const relativeDate = useFormatDateRelative(dateString);

  if (!displayLabel) {
    return (
      <div className={styles.Label}>
        <div className={styles.Title}>
          <Trans>Unnamed login</Trans>
        </div>
        <div className={styles.Subtitle}>
          <Trans>Login created {relativeDate}</Trans>
        </div>
      </div>
    );
  }

  return (
    <div className={styles.Label}>
      <div className={styles.Title}>{displayLabel}</div>
      {login.website && (
        <div className={styles.Subtitle}>
          <Trans>on {login.website}</Trans>
        </div>
      )}
    </div>
  );
}
