import { t, Trans } from '@lingui/macro';
import React from 'react';

import { VaultId } from 'backend-client-web';
import { PasswordGeneratorSettings } from 'client-core';
import { PasswordGenerator, usePasswordGeneration } from 'client-web-sdk';

import { GetAutosnatchTargetsResponse } from 'src/message';
import { Size } from 'src/types';

import AutosnatchTargetSelector, {
  shouldShowAutosnatchTargetSelector,
} from '../AutosnatchTargetSelector';

import styles from './MainPasswordFieldOverlay.module.css';
import PasswordFieldOverlay from './PasswordFieldOverlay';
import usePillText from './usePillText';

type Props = {
  generatorExpanded: boolean;
  initialPassword: string;
  passwordFieldHeight: number;
  passwordFieldWidth: number;
  passwordInsertedTimestampMs?: number;
  onButtonClick: () => void;
  onClose?: () => void;
  onResize?: (size: Size) => void;
  setPassword: (pw: string) => void;
  writeToClipboard: (text: string, clearAfterTimeout?: boolean) => void;
  savePasswordGeneratorSettings: (settings: PasswordGeneratorSettings) => void;
  passwordGeneratorSettings: PasswordGeneratorSettings | null;

  autosnatchTargetOptions: GetAutosnatchTargetsResponse;
  autosnatchTarget: VaultId | undefined;
  autosnatchTargetOverride: VaultId | undefined;
  setAutosnatchTarget: (vaultId: VaultId) => void;
  setAutosnatchTargetOverride: (vaultId: VaultId) => void;
};

export default function MainPasswordFieldOverlay({
  setPassword,
  initialPassword,
  passwordInsertedTimestampMs,
  generatorExpanded,
  onButtonClick,
  passwordGeneratorSettings,
  savePasswordGeneratorSettings,
  autosnatchTargetOptions,
  autosnatchTarget,
  autosnatchTargetOverride,
  setAutosnatchTarget,
  setAutosnatchTargetOverride,
  ...fieldProps
}: Props) {
  const { strengthIndication, ...passwordGeneration } = usePasswordGeneration({
    initialValue: initialPassword,
    initialSettings: passwordGeneratorSettings,
    onChange: setPassword,
  });

  const { generatorStrengthIndication, ...pillTextProps } = usePillText({
    passwordInsertedTimestampMs,
    generatorExpanded,
    passwordFieldWidth: fieldProps.passwordFieldWidth,
    strengthIndication,
    strengthIndicationFallback: t`Password`,
  });

  return (
    <PasswordFieldOverlay {...fieldProps} onButtonClick={onButtonClick} {...pillTextProps}>
      {generatorExpanded && (
        <div className={styles.Content}>
          <PasswordGenerator
            {...passwordGeneration}
            hideButtons
            oldPassword={undefined}
            writeToClipboard={(value) => fieldProps.writeToClipboard(value, true)}
            className={styles.PasswordGenerator}
            strengthIndication={generatorStrengthIndication ?? null}
            hideNewPasswordLabel
            saveSettings={savePasswordGeneratorSettings}
            savedSettings={passwordGeneratorSettings}
          />
          {autosnatchTargetOptions && shouldShowAutosnatchTargetSelector(autosnatchTargetOptions) && (
            <div className={styles.TargetSelectorGroup}>
              <div className={styles.ToLabel}>
                <Trans>Save to</Trans>
              </div>
              <AutosnatchTargetSelector
                className={styles.TargetSelector}
                autosnatchTarget={autosnatchTarget}
                autosnatchTargetOverride={autosnatchTargetOverride}
                setAutosnatchTargetOverride={setAutosnatchTargetOverride}
                autosnatchTargetOptions={autosnatchTargetOptions}
                setAutosnatchTarget={setAutosnatchTarget}
              />
            </div>
          )}
        </div>
      )}
    </PasswordFieldOverlay>
  );
}
