import React from 'react';

import AutosnatchNotificationOverlayContainer from './AutosnatchNotificationOverlayContainer';
import AutosnatchOverlayContainer from './AutosnatchOverlayContainer';
import LoginOverlay from './LoginOverlay';
import OverrideCreateOverlayContainer from './OverrideCreateOverlayContainer';
import PasswordFieldOverlayContainer from './PasswordFieldOverlayContainer';
import TotpInputOverlayContainer from './TotpInputOverlayContainer';
import TotpQrOverlayContainer from './TotpQrOverlayContainer';

export default function IframeApp() {
  const mode = new URLSearchParams(window.location.search).get('mode');
  if (mode === 'passwordGenerator') {
    return <PasswordFieldOverlayContainer showGenerator={true} />;
  }
  if (mode === 'repeatPassword') {
    return <PasswordFieldOverlayContainer showGenerator={false} />;
  }
  if (mode === 'login') {
    return <LoginOverlay />;
  }
  if (mode === 'autosnatch') {
    return <AutosnatchOverlayContainer />;
  }
  if (mode === 'totpQr') {
    return <TotpQrOverlayContainer />;
  }
  if (mode === 'overrideCreate') {
    return <OverrideCreateOverlayContainer />;
  }
  if (mode === 'totpInput') {
    return <TotpInputOverlayContainer />;
  }
  if (mode === 'autosnatchNotification') {
    return <AutosnatchNotificationOverlayContainer />;
  }
  throw new Error('Unknown iframe mode!');
}
