import { I18n } from '@lingui/core';
import { msg } from '@lingui/macro';
import { useLingui } from '@lingui/react';
import formatRelative from 'date-fns/formatRelative';
import { useMemo } from 'react';

import { ISODateString } from 'client-core';
import { getDateFnsLocale } from 'client-web-sdk';

export default function useFormatDateDayRelative(dateString: ISODateString) {
  const { _ } = useLingui();
  return useMemo(() => formatDateRelative(dateString, _), [_, dateString]);
}

export function formatDateRelative(dateString: ISODateString, t: I18n['_']) {
  const dateFnsLocale = getDateFnsLocale();
  // https://github.com/date-fns/date-fns/issues/1218#issuecomment-599182307
  const formatRelativeLocale = {
    lastWeek: () => `'${t(msg`last`)}' eeee`,
    yesterday: () => `'${t(msg`yesterday`)}'`,
    today: () => `'${t(msg`today at`)}' p`,
    tomorrow: () => `'${t(msg`tomorrow`)}'`,
    nextWeek: () => `'${t(msg`next`)}' eeee`,
    other: () => {
      const atAbsoluteDate = dateFnsLocale.formatRelative?.('other') ?? 'yyyy-MM-dd';
      return t(msg({ message: `${atAbsoluteDate}`, context: 'date-fns format string' }));
    },
  };
  const locale = {
    ...dateFnsLocale,
    formatRelative: (token: keyof typeof formatRelativeLocale) => formatRelativeLocale[token](),
  };
  return formatRelative(new Date(dateString), new Date(), {
    locale,
  });
}
