import { t } from '@lingui/macro';
import { useEffect, useState } from 'react';

import { trackError, sleep } from 'backend-client-web';

export default function usePillText({
  passwordInsertedTimestampMs,
  generatorExpanded,
  passwordFieldWidth,
  strengthIndication,
  strengthIndicationFallback,
}: {
  passwordInsertedTimestampMs?: number;
  generatorExpanded: boolean;
  passwordFieldWidth: number;
  strengthIndication: string | null;
  strengthIndicationFallback: string;
}): {
  eyeIcon?: 'on' | 'off';
  pillText?: string;
  successBackground?: boolean;
  spinner?: boolean;
  generatorStrengthIndication?: string | null;
} {
  const [animationStep, setAnimationStep] = useState<'generating' | 'done'>();
  useEffect(() => {
    async function runAnimation(offset: number) {
      setAnimationStep('generating');
      await sleep(offset + 700);
      setAnimationStep('done');
    }

    if (passwordInsertedTimestampMs && !animationStep) {
      runAnimation(passwordInsertedTimestampMs - Date.now()).catch(trackError);
    }
  }, [animationStep, passwordInsertedTimestampMs]);

  if (animationStep === 'generating') {
    return {
      pillText: t`Generate`,
      spinner: true,
    };
  }

  if (!passwordInsertedTimestampMs || !animationStep) {
    return {
      pillText: t`Generate`,
    };
  }

  const eyeIcon = generatorExpanded ? ('off' as const) : ('on' as const);

  if (passwordFieldWidth < 200) {
    return {
      eyeIcon,
      generatorStrengthIndication: strengthIndication,
      successBackground: true,
    };
  }

  if (!strengthIndication) {
    return {
      eyeIcon,
      pillText: strengthIndicationFallback,
      successBackground: true,
    };
  }

  return {
    eyeIcon,
    pillText: strengthIndication,
    successBackground: true,
  };
}
