import { assertExhaustive } from 'backend-client-web';
import {
  FormKindHintOverrideRule,
  LoginOverrideRule,
  OverrideRule,
  RegistrationOverrideRule,
} from 'persistable-types';

import { emptyLoginSelectors, emptyRegistrationSelectors } from './defaultValues';
import PredicateEdit from './PredicateEdit';
import SelectorInput from './SelectorInput';

export default function RuleEdit({
  rule,
  applied,
  setRule,
  deleteRule,
}: {
  rule: OverrideRule;
  applied: boolean;
  setRule: (r: OverrideRule) => void;
  deleteRule: () => void;
}) {
  return (
    <fieldset>
      <legend>
        {applied ? (
          <strong title="Applied on this page"> {rule.type}</strong>
        ) : (
          <span title="Not applied on this page"> {rule.type}</span>
        )}
      </legend>
      <PredicateEdit
        predicate={rule.predicate}
        setPredicate={(predicate) => setRule({ ...rule, predicate })}
      />
      {rule.type === 'loginForm' ? (
        <LoginRuleEdit rule={rule} setRule={setRule} />
      ) : rule.type === 'registrationForm' ? (
        <RegistrationRuleEdit rule={rule} setRule={setRule} />
      ) : rule.type === 'formKindHint' ? (
        <FormKindHintRuleEdit rule={rule} setRule={setRule} />
      ) : rule.type === 'noForm' ? (
        // Rule of type 'noForm'  has no editable properties
        <></>
      ) : (
        assertExhaustive(rule)
      )}
      <button type="button" onClick={deleteRule}>
        Delete rule
      </button>
    </fieldset>
  );
}

function LoginRuleEdit({
  rule,
  setRule,
}: {
  rule: LoginOverrideRule;
  setRule: (r: LoginOverrideRule) => void;
}) {
  return (
    <>
      <div>
        <label>
          <input
            type="checkbox"
            checked={rule.selectors !== null}
            onChange={(e) =>
              setRule({
                ...rule,
                selectors: e.target.checked ? rule.selectors ?? emptyLoginSelectors : null,
              })
            }
          />{' '}
          Has login form
        </label>
      </div>
      {rule.selectors !== null && (
        <div>
          <SelectorEdit
            name="Username"
            selector={rule.selectors.usernameElement}
            setSelector={(s) =>
              rule.selectors &&
              setRule({ ...rule, selectors: { ...rule.selectors, usernameElement: s } })
            }
            tagNames={['input']}
          />
          <SelectorEdit
            name="Password"
            selector={rule.selectors.passwordElement}
            setSelector={(s) =>
              rule.selectors &&
              setRule({ ...rule, selectors: { ...rule.selectors, passwordElement: s } })
            }
            tagNames={['input']}
          />
          <SelectorEdit
            name="Totp"
            selector={Array.isArray(rule.selectors.totpElements) ? '' : rule.selectors.totpElements}
            setSelector={(s) =>
              rule.selectors &&
              setRule({ ...rule, selectors: { ...rule.selectors, totpElements: s } })
            }
            tagNames={['input']}
          />
          <SelectorEdit
            name="Form"
            selector={rule.selectors.formElement}
            setSelector={(s) =>
              rule.selectors &&
              setRule({ ...rule, selectors: { ...rule.selectors, formElement: s } })
            }
            tagNames={['form']}
          />
          <SelectorEdit
            name="Submit"
            selector={rule.selectors.submitElement}
            setSelector={(s) =>
              rule.selectors &&
              setRule({ ...rule, selectors: { ...rule.selectors, submitElement: s } })
            }
            tagNames={['input', 'button']}
          />
          <SelectorEdit
            name="Stay logged in"
            selector={rule.selectors.stayLoggedInElement}
            setSelector={(s) =>
              rule.selectors &&
              setRule({ ...rule, selectors: { ...rule.selectors, stayLoggedInElement: s } })
            }
            tagNames={['input']}
          />
        </div>
      )}
    </>
  );
}

