function FormKindHintRuleEdit({
  rule,
  setRule,
}: {
  rule: FormKindHintOverrideRule;
  setRule: (r: FormKindHintOverrideRule) => void;
}) {
  return (
    <>
      <div>
        <label>
          Form kind:{' '}
          <select
            value={rule.kind}
            onChange={(e) => setRule({ ...rule, kind: e.currentTarget.value as any })}
          >
            {['login', 'registration'].map((kind) => (
              <option key={kind} value={kind}>
                {kind}
              </option>
            ))}
          </select>
        </label>
      </div>
    </>
  );
}

function SelectorEdit({
  name,
  selector,
  setSelector,
  tagNames,
}: {
  name: string;
  selector: string | null;
  setSelector: (v: string | null) => void;
  tagNames?: string[];
}) {
  return (
    <div>
      <label>
        <input
          type="checkbox"
          checked={selector !== null}
          onChange={(e) => setSelector(e.currentTarget.checked ? '' : null)}
          title="Element exists"
        />{' '}
        <span style={{ display: 'inline-block', width: '7.5rem' }}>{name}:</span>
      </label>{' '}
      <SelectorInput
        selector={selector ?? ''}
        disabled={selector === null}
        setSelector={setSelector}
        tagNames={tagNames}
      />
    </div>
  );
}
