import React, { useCallback, useEffect, useMemo, useState } from 'react';
import { debounce } from 'throttle-debounce';

import { VaultId, trackError } from 'backend-client-web';
import { PasswordGeneratorSettings } from 'client-core';
import { generateInitialPassword } from 'client-web-sdk';

import { ForwardIframeClientPort } from 'src/connections';
import MainPasswordFieldOverlay from 'src/entrypoints/iframe/components/passwordFieldOverlay/MainPasswordFieldOverlay';
import RepeatedPasswordFieldOverlay from 'src/entrypoints/iframe/components/passwordFieldOverlay/RepeatedPasswordFieldOverlay';
import useExtrinsicIframeSize from 'src/entrypoints/iframe/utils/getExtrinsicSizeFromLocation';
import { passwordGeneratorIframeStateDescriptor } from 'src/iframeState/descriptors/passwordGenerator';
import { repeatPasswordIframeStateDescriptor } from 'src/iframeState/descriptors/repeatPassword';
import useDispatchSetter from 'src/iframeState/hooks/useDispatchSetter';
import useIframeState from 'src/iframeState/hooks/useIframeState';
import { messageError, sendMessage } from 'src/message';
import { Size } from 'src/types';
import useAutosnatchSettings from 'src/util/react/useAutosnatchSettings';
import useAutosnatchTargetOverride from 'src/util/react/useAutosnatchTargetOverride';

import useContentScriptConnection from './useContentScriptConnection';

export default function PasswordFieldOverlayContainer({
  showGenerator,
}: {
  showGenerator: boolean;
}) {
  const port = useContentScriptConnection();

  const { width: passwordFieldWidth, height: passwordFieldHeight } = useExtrinsicIframeSize();

  if (!port) {
    return null;
  }

  return showGenerator ? (
    <PasswordGenerator
      port={port}
      passwordFieldHeight={passwordFieldHeight}
      passwordFieldWidth={passwordFieldWidth}
    />
  ) : (
    <RepeatedPassword
      port={port}
      passwordFieldHeight={passwordFieldHeight}
      passwordFieldWidth={passwordFieldWidth}
    />
  );
}

function PasswordGenerator({
  port,
  ...fieldProps
}: {
  port: ForwardIframeClientPort | undefined;
  passwordFieldWidth: number;
  passwordFieldHeight: number;
}) {
  const { iframeState, dispatch } = useIframeState(port, passwordGeneratorIframeStateDescriptor);

  const setIntrinsicSize = useDispatchSetter(dispatch, (size: Size) => ({
    type: 'setIntrinsicSize',
    size,
  }));

  const setHidden = useDispatchSetter(dispatch, (hidden: boolean) => ({
    type: 'setHidden',
    hidden,
  }));

  const setAutosnatchTargetOverride = useDispatchSetter(
    dispatch,
    (autosnatchTargetOverride: VaultId | undefined) => ({
      type: 'setAutosnatchTargetOverride',
      autosnatchTargetOverride,
    }),
  );

  const setExpanded = useDispatchSetter(dispatch, (expanded: boolean) => ({
    type: 'setExpanded',
    expanded,
  }));

  const setGeneratedPassword = useDispatchSetter(dispatch, (generatedPassword: string) => ({
    type: 'setGeneratedPassword',
    generatedPassword,
  }));

  const passwordInsertedTimestampMs = iframeState.passwordInsertedTimestampMs;
  const generatorExpanded = iframeState.expanded;

  const {
    passwordGeneratorSettings,
    savePasswordGeneratorSettings,
  } = usePasswordGeneratorSettings();
  const initialPassword = useMemo(
    () =>
      passwordGeneratorSettings === undefined
        ? undefined
        : generateInitialPassword(passwordGeneratorSettings),
    [passwordGeneratorSettings],
  );

  const setPassword = useMemo(() => debounce(1000, setGeneratedPassword), [setGeneratedPassword]);

  useEffect(() => {
    if (
      iframeState.insertInitialPassword &&
      initialPassword &&
      passwordInsertedTimestampMs === undefined
    ) {
      setGeneratedPassword(initialPassword);
    }
  }, [
    iframeState.insertInitialPassword,
    initialPassword,
    passwordInsertedTimestampMs,
    setGeneratedPassword,
  ]);

  const writeToClipboard = useCallback(
    (text, clearAfterTimeout?) => {
      dispatch({
        type: 'writeToClipboard',
        text,
        clearAfterTimeout,
      });
    },
    [dispatch],
  );

  const autosnatchSettings = useAutosnatchSettings();

  const overrideProps = useAutosnatchTargetOverride(
    iframeState.autosnatchTargetOverride,
    setAutosnatchTargetOverride,
  );

  return (
    <>
      {initialPassword !== undefined && passwordGeneratorSettings !== undefined && (
        <MainPasswordFieldOverlay
          initialPassword={initialPassword}
          setPassword={setPassword}
          writeToClipboard={writeToClipboard}
          generatorExpanded={generatorExpanded}
          passwordInsertedTimestampMs={passwordInsertedTimestampMs}
          passwordGeneratorSettings={passwordGeneratorSettings}
          savePasswordGeneratorSettings={savePasswordGeneratorSettings}
          onButtonClick={
            passwordInsertedTimestampMs
              ? () => setExpanded(!generatorExpanded)
              : () => {
                  setGeneratedPassword(initialPassword);
                }
          }
          onResize={setIntrinsicSize}
          onClose={() => setHidden(true)}
          {...fieldProps}
          {...autosnatchSettings}
          {...overrideProps}
        />
      )}
    </>
  );
}

function RepeatedPassword({
  port,
  ...fieldProps
}: {
  port: ForwardIframeClientPort | undefined;
  passwordFieldWidth: number;
  passwordFieldHeight: number;
}) {
  const { iframeState, dispatch } = useIframeState(port, repeatPasswordIframeStateDescriptor);

  const generatorExpanded = iframeState.expanded;
  const passwordInsertedTimestampMs = iframeState.passwordInsertedTimestampMs;

  const setIntrinsicSize = useDispatchSetter(dispatch, (size: Size) => ({
    type: 'setIntrinsicSize',
    size,
  }));

  const setExpanded = useDispatchSetter(dispatch, (expanded: boolean) => ({
    type: 'setExpanded',
    expanded,
  }));

  return (
    <RepeatedPasswordFieldOverlay
      {...fieldProps}
      onResize={setIntrinsicSize}
      onClose={() => dispatch({ type: 'hide' })}
      onButtonClick={() =>
        passwordInsertedTimestampMs
          ? setExpanded(!generatorExpanded)
          : dispatch({ type: 'insertPassword' })
      }
      generatorExpanded={generatorExpanded}
      passwordInsertedTimestampMs={passwordInsertedTimestampMs}
    />
  );
}

function usePasswordGeneratorSettings() {
  const [settings, setSettings] = useState<PasswordGeneratorSettings | null>();

  const savePasswordGeneratorSettings = useCallback((s: PasswordGeneratorSettings) => {
    sendMessage({
      type: 'SavePasswordGeneratorSettings',
      settings: s,
    }).catch(trackError);
  }, []);

  useEffect(() => {
    const updateSettings = async () => {
      const resp = await sendMessage({ type: 'GetPasswordGeneratorSettings' });
      if (resp !== messageError && resp) {
        setSettings(resp.settings);
      }
    };

    updateSettings().catch(trackError);
  }, []);

  return {
    passwordGeneratorSettings: settings,
    savePasswordGeneratorSettings,
  };
}
