import { getUrlSimilarity, parseUrlWithPsl, UriWithPsl } from 'client-web-sdk/getLoginsForUrl';

export default function getDisambiguatedLogins<T extends { websites: string[] }>(
  logins: readonly T[],
  url?: string,
): readonly (T & { website?: string })[] {
  const uriWithPsl = url !== undefined ? parseUrlWithPsl(url) : undefined;
  const loginsWithWebsite = logins.map((login) => ({
    ...login,
    website: getBestMatchingWebsite(login.websites, uriWithPsl),
  }));

  const firstWebsite = loginsWithWebsite[0]?.website;
  const websitesDiffer = loginsWithWebsite.some(({ website }) => website !== firstWebsite);

  // If there are logins with differing websites, return them with websites for disambiguation
  if (websitesDiffer) {
    return loginsWithWebsite;
  }

  // If the website is the same for all logins, there's no need to show it
  return logins;
}

function getBestMatchingWebsite(websites: string[], uriWithPsl?: UriWithPsl) {
  if (!uriWithPsl) {
    // We don't have a reference uri, just show the longest website we got
    let bestMatchingWebsite: string | undefined = undefined;
    for (const ws of websites) {
      if (!bestMatchingWebsite || ws.length > bestMatchingWebsite.length) {
        bestMatchingWebsite = ws;
      }
    }
    return bestMatchingWebsite;
  }

  // Pick the website that is most similar to the reference uri
  let bestMatchingWebsite: string | undefined = undefined;
  let bestMatchingWebsiteSimilarity = 0;
  for (const ws of websites) {
    const similarity = getUrlSimilarity(uriWithPsl, ws);
    if (!similarity) {
      continue;
    }
    if (
      similarity > bestMatchingWebsiteSimilarity ||
      // If we have multiple urls with same similarity, prefer the shortest one because it's going to be
      // easiest for the user to read that one.
      (similarity === bestMatchingWebsiteSimilarity &&
        (!bestMatchingWebsite || ws.length < bestMatchingWebsite?.length))
    ) {
      bestMatchingWebsite = ws;
      bestMatchingWebsiteSimilarity = similarity;
    }
  }
  return bestMatchingWebsite;
}
