import { VaultId } from 'backend-client-web';

import { Size } from 'src/types';

import { IframeStateDescriptor } from './types';

export type AutosnatchIframeState = {
  intrinsicSize: Size;
  expanded: boolean;
  visible: boolean;
  autosnatchTargetOverride?: VaultId;
  pageHasLogins: boolean;
};

export type AutosnatchIframeStateDescriptorType = {
  mode: 'autosnatch';
  state: AutosnatchIframeState;
  action:
    | { type: 'setIntrinsicSize'; size: Size }
    | { type: 'setExpanded'; expanded: boolean }
    | { type: 'setVisible'; visible: boolean }
    | { type: 'setAutosnatchTargetOverride'; autosnatchTargetOverride?: VaultId }
    | { type: 'showPasswordGenerator' }
    | { type: 'showLoginOverlay' };
};

export const autosnatchIframeStateDescriptor: IframeStateDescriptor<AutosnatchIframeStateDescriptorType> = {
  mode: 'autosnatch',
  initialState: {
    intrinsicSize: { width: 0, height: 0 },
    expanded: false,
    visible: false,
    pageHasLogins: false,
  },
  reducer: (state, action) => {
    switch (action.type) {
      case 'setIntrinsicSize':
        return { ...state, intrinsicSize: action.size };
      case 'setExpanded':
        return { ...state, expanded: action.expanded };
      case 'setVisible':
        return { ...state, visible: action.visible };
      case 'setAutosnatchTargetOverride':
        return { ...state, autosnatchTargetOverride: action.autosnatchTargetOverride };
      case 'showPasswordGenerator':
      case 'showLoginOverlay':
        return { ...state, expanded: false };
    }
  },
};
